import React, { Component } from 'react';
import { withTranslation } from 'react-i18next';

// import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
// import * as findParkingSpaceActionCreators from '../../actions/findParkingSpace';

import GridType5 from '../../components/102Grids/GridType5';

class Block extends Component {
    render() {
        return (
            <GridType5
                id={this.props.id}
                count={this.props.count}
                verified={this.props.verified}
                handleVerified={this.props.handleVerified}
                wishlist={this.props.wishlist}
                handleWishlist={this.props.handleWishlist}
                space={this.props.space}
            />
        )
    }
}

class Cluster extends Component {
    render() {
        let rows = [];
        for (let i = 0; i < 1; i++) {
            rows.push(
                <div className="full left" key={i}>
                    <Block
                        key={i}
                        id={i}
                        count={i}
                        verified={this.props.verified}
                        handleVerified={this.props.handleVerified}
                        wishlist={this.props.wishlist}
                        handleWishlist={this.props.handleWishlist}
                    />
                </div>
            )
        }
        return (rows);
    }
}

class HourlyPayment extends Component {

    constructor(props) {

        super(props);

        //     const { dispatch } = props;

        //     // DISPATCH TO ACTIONS
        //     this.toggleAvailableDays = bindActionCreators(listParkingSpaceActionCreators.toggleAvailableDays, dispatch);

        this.state = {
            carTypes: ['Indoor', 'EV'],
            verified: {
                2: true
            },
            wishlist: {
                1: true
            },
            changeVehicle: false
        }
    }

    handleVerified = (id) => {
        this.setState({
            verified: {
                ...this.state.verified,
                [id]: !this.state.verified[id]
            }
        });
    }

    handleWishlist = (id) => {
        this.setState({
            wishlist: {
                ...this.state.wishlist,
                [id]: !this.state.wishlist[id]
            }
        });
    }

    handlechangeVehicle = () => {
        this.setState({
            ...this.state,
            changeVehicle: !this.state.changeVehicle
        })
    }

    render() {

        // const { listParkingSpace, 
        //     //translate 
        // } = this.props;

        return (
            <div className="wrapper-container payment">

                <div className="containerMain">

                    <div className="content clearfix">

                        <div className="full left">
                            <div>
                                <h2>Confirmation and Payment</h2>
                            </div>
                        </div>

                        <div className="sep-0"></div>

                        <div className="fifty left">
                            <div>
                                <div className="border-box clearfix">
                                    <Cluster
                                        verified={this.state.verified}
                                        handleVerified={this.handleVerified}
                                        wishlist={this.state.wishlist}
                                        handleWishlist={this.handleWishlist}
                                    />

                                    <div className="sep-20"></div>

                                    <hr />

                                    <div className="sep-20"></div>

                                    <dl className="clearfix">
                                        <dt>01/01/2018 - 31/01/2018</dt>
                                        <dd>HKD 1,900</dd>
                                        <div className="sep-0"></div>
                                        <dt>Deposit</dt>
                                        <dd>HKD 1,900</dd>
                                        <div className="sep-0"></div>
                                        <dt><b>Total Amount</b></dt>
                                        <dd><b>HKD 3,800</b></dd>
                                    </dl>
                                </div>

                                <div className="sep-20"></div>
                            </div>
                        </div>

                        <div className="fifty left">
                            <div>
                                <div className="wrapper">
                                    <h4><span>1</span>Vehicle</h4>

                                    <div className="wrap clearfix">
                                        <div className="full">
                                            <div>
                                                {false &&
                                                <select>
                                                    <option value="">Please select</option>
                                                </select>
                                                }

                                                {(!this.state.changeVehicle) &&
                                                    <div className="sep-20"></div>
                                                }
                                                {(!this.state.changeVehicle) &&
                                                    <span className="additional"
                                                        onClick={() => this.handlechangeVehicle()}
                                                    >add vehicle</span>
                                                }

                                                {(this.state.changeVehicle) &&
                                                    <div className="sep-20"></div>
                                                }
                                                {(this.state.changeVehicle) &&
                                                    <select>
                                                        <option>Vehicle type</option>
                                                    </select>
                                                }
                                                {(this.state.changeVehicle) &&
                                                    <div className="sep-20"></div>
                                                }
                                                {(this.state.changeVehicle) &&
                                                    <input type="text" placeholder="Vehicle registration mark" />
                                                }
                                                {(this.state.changeVehicle) &&
                                                    <div className="sep-20"></div>
                                                }
                                                {(this.state.changeVehicle) &&
                                                    <button className="button grey first"
                                                        onClick={() => this.handlechangeVehicle()}
                                                    >Cancel</button>
                                                }
                                                {(this.state.changeVehicle) &&
                                                    <button className="button">Add</button>
                                                }
                                            </div>
                                        </div>
                                    </div>

                                    {/*TODO * add change vehicle type*/}
                                    {/* <div className="wrap clearfix">
                                        <div className="full">
                                            <div>
                                                <input type="text" placeholder="Vehicle registration mark" />
                                            </div>
                                        </div>
                                    </div>

                                    <div className="wrap clearfix">
                                        <div className="full">
                                            <div>
                                                <a href="/" className="button">Cancel</a><a href="/" className="button">Add</a>
                                            </div>
                                        </div>
                                    </div> */}
                                </div>

                                <div className="wrapper">
                                    <h4><span>2</span>Octopus card Number</h4>

                                    <div className="wrap clearfix">
                                        <div className="full">
                                            <div>
                                                <input type="text" />
                                            </div>
                                        </div>
                                    </div>

                                    <div className="checkboxFive">
                                        <div>
                                            <input type="checkbox" id="AddOctopus"
                                            />
                                            <label htmlFor="AddOctopus"></label>
                                        </div>
                                        <label htmlFor="AddOctopus">Add this card into account</label>
                                    </div>
                                </div>

                                <div className="wrapper">
                                    <div className="cardIcons">
                                        <span><img src={require('../../images/mobile/03_find_parking_space/icon_visa.png')} alt="" /></span>
                                        <span><img src={require('../../images/mobile/03_find_parking_space/icon_master.png')} alt="" /></span>
                                    </div>

                                    <h4><span>3</span>Payment</h4>

                                    <div className="wrap clearfix">
                                        <div className="full">
                                            <div>
                                                <h5>Credit Card number</h5>
                                                <input type="text" />
                                            </div>
                                        </div>
                                    </div>

                                    <div className="wrap clearfix">
                                        <div className="full">
                                            <div>
                                                <h5>Name of Card Holder</h5>
                                                <input type="text" />
                                            </div>
                                        </div>
                                    </div>

                                    <div className="wrap clearfix">
                                        <div className="fifty">
                                            <div>
                                                <h5>Expiry Date</h5>
                                                <input type="text" placeholder="YY" />
                                            </div>
                                        </div>
                                        <div className="fifty">
                                            <div>
                                                <h5>&nbsp;</h5>
                                                <input type="text" placeholder="MM" />
                                            </div>
                                        </div>
                                    </div>

                                    <div className="wrap clearfix">
                                        <div className="full">
                                            <div>
                                                <h5>CVC</h5>
                                                <input type="text" />
                                            </div>
                                        </div>
                                    </div>

                                    {/* <div className="secure">
                                        <span><img className="norton" src={require('../../images/mobile/03_find_parking_space/icon_norton.png')} alt="" /></span>
                                        <span><img className="lock" src={require('../../images/mobile/03_find_parking_space/icon_secure.png')} alt="" /> Secure payment</span>
                                    </div> */}

                                    <div className="sep-10"></div>

                                    <div className="checkboxFive">
                                        <div>
                                            <input type="checkbox" id="AddCreditcard"
                                            />
                                            <label htmlFor="AddCreditcard"></label>
                                        </div>
                                        <label htmlFor="AddCreditcard">Add this credit card to your account for faster booking</label>
                                    </div>

                                    <div className="sep-20"></div>

                                    <div className="checkboxFive">
                                        <div>
                                            <input type="checkbox" id="Accept"
                                            />
                                            <label htmlFor="Accept"></label>
                                        </div>
                                        <label htmlFor="Accept">I have read and accepted the Parker Agreement, Parking Space License Aggreement and Cancellation Policy. I also agree to pay the shown total amount.</label>
                                    </div>

                                    <div className="sep-20"></div>

                                    <table className="verifiedGuarantee">
                                        <tbody>
                                            <tr>
                                                <td> <img src={require('../../images/mobile/03_find_parking_space/icon_verified.png')} alt="" /></td>
                                                <td>Land search <span>Verified</span></td>
                                            </tr>
                                            <tr>
                                                <td> <img src={require('../../images/mobile/03_find_parking_space/icon_guarantee.png')} alt="" /></td>
                                                <td>You are always protected by our Parker Protection <span>Guarantee</span></td>
                                            </tr>
                                        </tbody>
                                    </table>

                                    <div className="sep-20"></div>

                                    <a href="/" className="button bookNow">Pay Now</a>
                                </div>
                            </div>
                        </div>

                        <div className="sep-30"></div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => (
    {
        listParkingSpace: state.listParkingSpace
    }
);

export default withTranslation()(connect(mapStateToProps)(HourlyPayment));
