class SpaceOne extends Component {

    constructor(props) {

        super(props);

        const { dispatch } = props;

        // DISPATCH TO ACTIONS
        this.toPayment = bindActionCreators(FindParkingSpaceActionCreators.toPayment, dispatch);

        this.state = {
            // MUST KEEN BELOW STATE STRUCTURE START
            view: 'photo',
            pressedBookNow: false,
            BookingType: 'hourly',
            supportTypes: [],
            minPrice: {
                'type': 'hourly',
                'price': 0,
            },
            wishlist: {
                1: true
            },
            formSubmit: false,
            toBookHourly: {
                startDate: null,
                endDate: null,
                startTime: null,
                endTime: null,
                price: 20,
                calculation: {
                    success: null,
                    from: '',
                    to: '',
                    hours: 0,
                    amount: 0,
                    total: 0
                }
            },
            toBookMonthly: {
                startDate: null,
                endDate: null,
                reserved: false,
                price: 20,
                calculation: {
                    success: null,
                    from: '',
                    to: '',
                    hours: 0,
                    amount: 0,
                    total: 0
                }
            },
            toBookTimeshare: {
                startDate: null,
                endDate: null,
                reserved: false,
                price: 20,
                calculation: {
                    success: null,
                    from: '',
                    to: '',
                    hours: 0,
                    amount: 0,
                    total: 0
                }
            },
            // MUST KEEN BELOW STATE STRUCTURE END

            data: {
                accesstype: [""],
                address: "",
                booked: [],
                cartype: [""],
                description: [""],
                features: [""],
                floor: "",
                height: "",
                // hourly: {
                //     always: true,
                //     available: [],
                //     avatype: ["MonFri", "SatSunPH"],
                //     blocked: [],
                //     days: [],
                //     deposit: 200,
                //     from: "2019-01-01",
                //     minbooking: 1,
                //     to: "2019-12-30"
                // },
                id: "",
                images: [""],
                instant: true,
                lat: "",
                lng: "",
                // monthly: {
                //     blocked: [],
                //     from: "2019-03-01",
                //     minbooking: 1,
                //     price: 1500,
                //     to: "2019-09-20",
                // },
                owner: {
                    name: "",
                    _id: "",
                    created: "",
                },
                restrictions: ["sdf"],
                spaceno: "170",
                // timeshare: {
                //     available: [],
                //     avatype: ["MonFri", "SatSunPH"],
                //     blocked: [],
                //     from: "2019-04-03",
                //     minbooking: 1,
                //     price: 1000,
                //     to: "2019-05-10",
                // },
                title: "",
            }
        }

        this.handleChangeView = this.handleChangeView.bind(this);
        this.updateState = this.updateState.bind(this);
        this.handleReserved = this.handleReserved.bind(this);
        this.updateCalculation = this.updateCalculation.bind(this);
        this.goToPayment = this.goToPayment.bind(this);
        this.handleCalculation = this.handleCalculation.bind(this);
    }

    componentDidMount = () => {
        this.ApiGetSpace();
    }

    // General
    handleChangeView = (e, data1, data2) => {
        e.preventDefault();
        this.setState({
            ...this.state,
            [data1]: data2
        })
    }

    handleWishlist = (id) => {
        this.setState({
            wishlist: {
                ...this.state.wishlist,
                [id]: !this.state.wishlist[id]
            }
        });
    }

    handleBookNow = (e) => {
        e.preventDefault();
        this.setState({
            ...this.state,
            pressedBookNow: !this.state.pressedBookNow
        })
    }

    handleBookingType = (value) => {
        this.setState({
            ...this.state,
            BookingType: value
        })
    }

    // Hourly, Monthy, Timeshare General
    updateState = (key1, key2, value) => {
        this.setState({
            ...this.state,
            [key1]: {
                ...this.state[key1],
                [key2]: value
            }
        }, () => {
            this.updateCalculation(key1);
        })
    }

    // Hourly, Monthy, Timeshare General
    handleReserved = (key1, v) => {
        this.setState({
            ...this.state,
            [key1]: {
                ...this.state[key1],
                reserved: v
            }
        }, () => {
            this.updateCalculation(key1);
        })
    }

    handleCalculation = (type, calculation) => {
        // console.log(type, calculation);
        switch (type) {
            case 'hourly':
                this.setState({
                    ...this.state,
                    toBookHourly: {
                        ...this.state.toBookHourly,
                        calculation: calculation
                    }
                })
                break;
            case 'monthly':
                this.setState({
                    ...this.state,
                    toBookMonthly: {
                        ...this.state.toBookMonthly,
                        calculation: calculation
                    }
                })
                break;
            default:
                this.setState({
                    ...this.state,
                    toBookTimeshare: {
                        ...this.state.toBookTimeshare,
                        calculation: calculation
                    }
                })
        }
    }

    // update calculation
    updateCalculation = (key1) => {
        const access_token = this.props.members.Login.userToken,
            space_id = this.props.space_id;

        if (key1 === 'toBookHourly') {
            // Hourly
            if (this.state.toBookHourly.startDate && this.state.toBookHourly.endDate && this.state.toBookHourly.startTime && this.state.toBookHourly.endTime) {
                Helper.HourlyCalculateTotal(this.state.toBookHourly, space_id, access_token, this.handleCalculation);
            }
        } else if (key1 === 'toBookMonthly') {
            // Monthly
            if (this.state.toBookMonthly.startDate && this.state.toBookMonthly.endDate) {
                Helper.MonthlyCalculateTotal(this.state.toBookMonthly, space_id, access_token, this.handleCalculation);

                // let newCalculateMonthly = Helper.MonthlyCalculateTotal(this.state.toBookMonthly);
                //
                // this.setState({
                //     ...this.state,
                //     toBookMonthly: {
                //         ...this.state.toBookMonthly,
                //         calculation: newCalculateMonthly
                //     }
                // })
            }
        } else {
            // Timeshare
            if (this.state.toBookTimeshare.startDate && this.state.toBookTimeshare.endDate) {
                Helper.TimeshareCalculateTotal(this.state.toBookTimeshare, space_id, access_token, this.handleCalculation);

                // let newCalculateTimeshare = Helper.TimeshareCalculateTotal(this.state.toBookTimeshare);
                //
                // this.setState({
                //     ...this.state,
                //     toBookTimeshare: {
                //         ...this.state.toBookTimeshare,
                //         calculation: newCalculateTimeshare
                //     }
                // })
            }
        }
    }

    // Hourly, Monthy, Timeshare General
    goToPayment = (e) => {
        e.preventDefault();

        let data = this.state;
        this.toPayment(data);

        this.setState({
            formSubmit: true
        })
    }

    ApiGetSpace = () => {
        var body = {
            spaceID: this.props.space_id
        }

        fetch(Config.API_URL + 'space/' + body.spaceID
            //     , {
            //     method: 'get',
            //     headers: new Headers({
            //         // 'Authorization': 'Basic ' + btoa(Config.UAT_API_LOGIN + ':' + Config.UAT_API_PASSWORD),
            //         // 'Accept': 'application/json',
            //         // 'Content-Type': 'application/json'
            //     }),
            //     body: JSON.stringify(body)
            // }
        ).then(r => r.json().then(data => ({ status: r.status, body: data })
        )).then(obj => {
            if (obj.status === 200) {
                console.log(obj.body);
                // console.log(obj.body.hourly);
                console.log(obj.body.monthly);
                // console.log(obj.body.timeshare);

                let booking_type;
                let supportTypes = [];
                let minPrice = {
                    'type': '',
                    'price': 0,
                };

                if (typeof (obj.body.hourly.from) !== "undefined" && obj.body.hourly.from !== "") {
                    supportTypes.push('hourly');
                    if (typeof (booking_type) === "undefined") {
                        booking_type = 'hourly';
                        minPrice.type = 'hourly';
                        minPrice.price = lowestPrice(obj.body.hourly.days);
                    }
                }

                if (typeof (obj.body.monthly.from) !== "undefined" && obj.body.monthly.from !== "") {
                    supportTypes.push('monthly');
                    if (typeof (booking_type) === "undefined") {
                        booking_type = 'monthly';
                        minPrice.type = 'monthly';
                        minPrice.price = obj.body.monthly.price;
                    }
                }

                if (typeof (obj.body.timeshare.from) !== "undefined" && obj.body.timeshare.from !== "") {
                    supportTypes.push('timeshare');
                    if (typeof (booking_type) === "undefined") {
                        booking_type = 'timeshare';
                        minPrice.type = 'timeshare';
                        minPrice.price = obj.body.timeshare.price;
                    }
                }

                // console.log(supportTypes);
                this.setState({
                    ...this.state,
                    data: obj.body,
                    BookingType: booking_type,
                    minPrice: minPrice,
                    supportTypes: supportTypes,
                    pressedBookNow: true,

                });
            } else if (obj.status === 400) {

                this.setState({
                    errors: obj.body.messages
                });
            }

        }).catch(error => {
            console.log(error)
        });
    }

    render() {
        const {
            // t,
            i18n
        } = this.props;

        if (this.state.formSubmit) {

            let spaceID = this.props.space_id;
            let queryString;

            switch (this.state.BookingType) {
                case 'hourly':
                    queryString =
                        'from=' + this.state.toBookHourly.startDate.utcOffset(8).format("YYYY-MM-DD")
                        + '&from_time=' + this.state.toBookHourly.startTime
                        + '&to=' + this.state.toBookHourly.endDate.utcOffset(8).format("YYYY-MM-DD")
                        + '&to_time=' + this.state.toBookHourly.endTime
                        + '&booking_type=' + this.state.BookingType;
                    break;
                case 'monthly':
                    queryString =
                        'from=' + this.state.toBookMonthly.startDate.utcOffset(8).format("YYYY-MM-DD")
                        + '&to=' + this.state.toBookMonthly.endDate.utcOffset(8).format("YYYY-MM-DD")
                        + '&booking_type=' + this.state.BookingType;
                    break;
                case 'timeshare':
                    queryString =
                        'from=' + this.state.toBookTimeshare.startDate.utcOffset(8).format("YYYY-MM-DD")
                        + '&to=' + this.state.toBookTimeshare.endDate.utcOffset(8).format("YYYY-MM-DD")
                        + '&booking_type=' + this.state.BookingType;
                    break;
                default:
                    queryString =
                        'from=' + this.state.toBookHourly.startDate.utcOffset(8).format("YYYY-MM-DD")
                        + '&to=' + this.state.toBookHourly.endDate.utcOffset(8).format("YYYY-MM-DD")
                        + '&booking_type=' + this.state.BookingType;
            }

            return <Redirect to={"/" + i18n.language + "/payment/" + spaceID + "?" + queryString} />;
        }

        return (
            <div className="wrapper-container detailShare">
                <div className="containerMain">
                    <div className="content clearfix">
                        <div className="information left">
                            <div className="text">
                                <h3>{this.state.data.title}</h3>
                                <h4>{this.state.data.address}</h4>
                                <div className="sep-0"></div>
                                <Reviews reviews={this.state.reviews} score={this.state.score} />

                                <div className="sep-15"></div>

                                <Tags
                                    tags={this.state.data.description}
                                />

                                <div className="sep-20"></div>
                            </div>
                            <ViewChangeWithSlider
                                view={this.state.view}
                                handleChangeView={this.handleChangeView}
                                wishlist={this.state.wishlist}
                                handleWishlist={this.handleWishlist}
                                lng={this.state.data.lng}
                                lat={this.state.data.lat}
                            />
                            <div className="sep-40"></div>

                            <div className="landlord clearfix">

                                <div className="fifty clearfix">

                                    <div className="image">
                                        <img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" />
                                    </div>
                                    <div className="text">
                                        <h3>{this.state.data.owner.name}</h3>
                                        <div className="memberSince">Member Since <span>{mongoIdTimestamp(this.state.data.owner._id, "YYYY-MM-DD")}</span></div>
                                        <div className="sep-10"></div>
                                        <a href={"/" + i18n.language + "/contactOwner"} className="button">Contact Owner</a>
                                    </div>
                                </div>

                                <div className="fifty clearfix"></div>
                            </div>
                        </div>

                        {(!this.state.pressedBookNow) &&
                            <BookNow
                                price={this.state.minPrice.price}
                                unit={this.state.minPrice.type === 'hourly' ? 'hour' : 'month'}
                                instant={this.state.instant}
                                handleBookNow={this.handleBookNow}
                            />
                        }

                        {this.state.pressedBookNow &&
                            this.state.BookingType === 'hourly' &&
                            typeof (this.state.data.hourly) !== "undefined" &&
                            typeof (this.state.data.hourly.from) !== "undefined" &&
                            this.state.data.hourly.from !== "" &&
                            <DetailHourly
                                hourlyData={this.state.data.hourly}
                                BookingType={this.state.BookingType}
                                handleBookingType={this.handleBookingType}
                                supportTypes={this.state.supportTypes}

                                startDate={this.state.toBookHourly.startDate}
                                endDate={this.state.toBookHourly.endDate}
                                startTime={this.state.toBookHourly.startTime}
                                endTime={this.state.toBookHourly.endTime}
                                updateState={this.updateState}

                                calculation={this.state.toBookHourly.calculation}
                                goToPayment={this.goToPayment}


                            />
                        }

                        {this.state.pressedBookNow &&
                            this.state.BookingType === 'monthly' &&
                            typeof (this.state.data.monthly) !== "undefined" &&
                            typeof (this.state.data.monthly.from) !== "undefined" &&
                            this.state.data.monthly.from !== "" &&
                            <DetailMonthly
                                BookingType={this.state.BookingType}
                                handleBookingType={this.handleBookingType}
                                supportTypes={this.state.supportTypes}

                                typeDetail={this.state.data.monthly}
                                startDate={this.state.toBookMonthly.startDate}
                                endDate={this.state.toBookMonthly.endDate}
                                updateState={this.updateState}

                                reserved={this.state.toBookMonthly.reserved}
                                handleReserved={this.handleReserved}

                                calculation={this.state.toBookMonthly.calculation}
                                goToPayment={this.goToPayment}
                            />
                        }

                        {this.state.pressedBookNow &&
                            this.state.BookingType === 'timeshare' &&
                            typeof (this.state.data.timeshare) !== "undefined" &&
                            typeof (this.state.data.timeshare.from) !== "undefined" &&
                            this.state.data.timeshare.from !== "" &&
                            <DetailTimeshare
                                BookingType={this.state.BookingType}
                                handleBookingType={this.handleBookingType}
                                supportTypes={this.state.supportTypes}

                                typeDetail={this.state.data.timeshare}
                                startDate={this.state.toBookTimeshare.startDate}
                                endDate={this.state.toBookTimeshare.endDate}
                                updateState={this.updateState}

                                reserved={this.state.toBookTimeshare.reserved}
                                handleReserved={this.handleReserved}

                                calculation={this.state.toBookTimeshare.calculation}
                                goToPayment={this.goToPayment}
                            />
                        }

                        <div className="sep-30"></div>

                        <div className="wrapper-tabs">
                            <Tabs defaultActiveKey={1} id="uncontrolled-tab-example">
                                <Tab eventKey={1} title="Details">
                                    <div className="inner">
                                        <h5>Space suitable for</h5>

                                        <SpaceSuitableFor
                                            cartype={this.state.data.cartype}
                                        />


                                        <div className="sep-25"></div>

                                        <h5>Feature</h5>

                                        <Features
                                            features={this.state.data.features}
                                        />

                                        <div className="sep-25"></div>
                                        <h5>Restrictions</h5>
                                        <Restrictions
                                            restrictions={this.state.data.restrictions[0]}
                                        />

                                        <div className="sep-25"></div>
                                        <h5>Access Type</h5>
                                        <AccessType
                                            accesstype={this.state.data.accesstype[0]}
                                        />
                                    </div>
                                </Tab>
                                <Tab eventKey={2} title="Reviews">
                                    <div className="inner">
                                        No Review Yet
                                    </div>
                                </Tab>
                            </Tabs>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => (
    {
        members: state.members,
        findParkingSpace: state.findParkingSpace
    }
);

export default withTranslation()(connect(mapStateToProps)(SpaceOne));
