import React, { Component } from 'react';

import InputRange from 'react-input-range';

class SortbyFilterDesktop2 extends Component {

    render() {

        // console.log(this.props.filter);

        return (
            <div>
                <ul className={"sortbyFilterDesktop2 clearfix " + (this.props.desktopFilter.showDesktopFilter ? 'active' : '')}>
                    <li><span className={(this.props.desktopFilter.showSortby ? "active " : "")}
                        onClick={(e) => this.props.handleDesktopFilter('sortBy')}
                    >Sort By</span>

                        <ul className={"menuSortby " + (this.props.desktopFilter.showSortby ? "active " : "")}>
                            <div className="contentSortby">
                                <ul className="clearfix">
                                    <div className="oneFourth left">
                                        <li className={"price " + (this.props.sortby.price ? "active" : "")}
                                            onClick={(e) => this.props.handleSortbyOptions('price')}>Price (Decending)</li>
                                    </div>
                                    <div className="oneFourth left">
                                        <li className={"rating " + (this.props.sortby.rating ? "active" : "")}
                                            onClick={(e) => this.props.handleSortbyOptions('rating')}>Rating (Decending)</li>
                                    </div>
                                    <div className="oneFourth left">
                                        <li className={"distance " + (this.props.sortby.distance ? "active" : "")}
                                            onClick={(e) => this.props.handleSortbyOptions('distance')}>Distance (Closest first)</li>
                                    </div>
                                    <div className="oneFourth left">
                                        <li className={"review " + (this.props.sortby.review ? "active" : "")}
                                            onClick={(e) => this.props.handleSortbyOptions('review')}>No. of Reviews (Decending)</li>
                                    </div>
                                </ul>
                            </div>
                        </ul>
                    </li >
                    <li><span className={"reverse " + (this.props.desktopFilter.showFilter ? "active " : "")}
                        onClick={(e) => this.props.handleDesktopFilter('filter')}
                    >Filter</span>

                        <ul className={"menuFilter " + (this.props.desktopFilter.showFilter ? "active " : "")}>
                            <div className="contentFilter clearfix">

                                <h5>Class of Vehicle</h5>
                                <ul className="booking-options clearfix">
                                    <div className="oneFifth">
                                        <li className={(this.props.filter.cartype === "PrivateCar" ? "active" : "")}
                                            onClick={(e) => this.props.handleFilterSelect('cartype', 'PrivateCar')}>
                                            <div className={"icon privateCar " + (this.props.filter.cartype === "PrivateCar" ? "active" : "")}></div>
                                            <h5>Private Car</h5>
                                        </li>
                                    </div>
                                    <div className="oneFifth">
                                        <li className={(this.props.filter.cartype === "Motorcycle" ? "active" : "")}
                                            onClick={(e) => this.props.handleFilterSelect('cartype', 'Motorcycle')}>
                                            <div className={"icon motorcycle " + (this.props.filter.cartype === "Motorcycle" ? "active" : "")}></div>
                                            <h5>Motorcycle</h5>
                                        </li>
                                    </div>
                                    <div className="oneFifth">
                                        <li className={(this.props.filter.cartype === "Van" ? "active" : "")}
                                            onClick={(e) => this.props.handleFilterSelect('cartype', 'Van')}>
                                            <div className={"icon van " + (this.props.filter.cartype === "Van" ? "active" : "")}></div>
                                            <h5>Van</h5>
                                        </li>
                                    </div>
                                    <div className="oneFifth">
                                        <li className={(this.props.filter.cartype === "Lorry" ? "active" : "")}
                                            onClick={(e) => this.props.handleFilterSelect('cartype', 'Lorry')}>
                                            <div className={"icon lorry " + (this.props.filter.cartype === "Lorry" ? "active" : "")}></div>
                                            <h5>Lorry</h5>
                                        </li>
                                    </div>
                                    <div className="oneFifth">
                                        <li className={(this.props.filter.cartype === "Others" ? "active" : "")}
                                            onClick={(e) => this.props.handleFilterSelect('cartype', 'Others')}>
                                            <div className={"icon others " + (this.props.filter.cartype === "Others" ? "active" : "")}></div>
                                            <h5>Other</h5>
                                        </li>
                                    </div>
                                </ul>

                                <div className="sep-20"></div>

                                <h5>Feature</h5>
                                <ul className="booking-options clearfix special">
                                    <div className="oneThird">
                                        <li className={(this.props.filter.features1 === 'Indoor' ? "active" : "")}
                                            onClick={(e) => this.props.handleFilterSelect('features1', 'Indoor')}>
                                            <div className={"icon indoor " + (this.props.filter.features1.indexOf('Indoor') > -1 ? "active" : "")}></div>
                                            <h5>Indoor</h5>
                                        </li>
                                    </div>
                                    <div className="oneThird">
                                        <li className={(this.props.filter.features1 === 'WithCovers' ? "active" : "")}
                                            onClick={(e) => this.props.handleFilterSelect('features1', 'WithCovers')}>
                                            <div className={"icon withCovers " + (this.props.filter.features1.indexOf('WithCovers') > -1 ? "active" : "")}></div>
                                            <h5>With Covers</h5>
                                        </li>
                                    </div>
                                    <div className="oneThird">
                                        <li className={(this.props.filter.features1 === 'NoCovers' ? "active" : "")}
                                            onClick={(e) => this.props.handleFilterSelect('features1', 'NoCovers')}>
                                            <div className={"icon noCovers " + (this.props.filter.features1.indexOf('NoCovers') > -1 ? "active" : "")}></div>
                                            <h5>No Covers</h5>
                                        </li>
                                    </div>
                                </ul>
                                <ul className="booking-options ev special">
                                    <div className="oneThird">
                                        <li className={(this.props.filter.features2.indexOf('EV') > -1 ? "active" : "")}
                                            onClick={(e) => this.props.handleFilterOptions('features2', 'EV')}>
                                            <div className={"icon ev " + (this.props.filter.features2.indexOf('EV') > -1 ? "active" : "")}></div>
                                            <h5>EV</h5>
                                        </li>
                                    </div>
                                    <div className="oneThird">
                                        <li className={(this.props.filter.features2.indexOf('HeadRoom') > -1 ? "active" : "")}
                                            onClick={(e) => this.props.handleFilterOptions('features2', 'HeadRoom')}>
                                            <div className={"icon headRoom " + (this.props.filter.features2.indexOf('HeadRoom') > -1 ? "active" : "")}></div>
                                            <h5>Head Room Maximum</h5>
                                        </li>
                                    </div>
                                    <div className="oneThird align-bottom align-left">
                                        {this.props.filter.features2.indexOf('HeadRoom') > -1 &&
                                            <li>
                                                <select
                                                    value={this.props.filter.height}
                                                    onChange={(e) => this.props.handleFilterSelect('height', e.target.value)}>
                                                    <option value="1.7">1.7 m</option>
                                                    <option value="1.8">1.8 m</option>
                                                    <option value="1.9">1.9 m</option>
                                                    <option value="2.0">2.0 m</option>
                                                    <option value="2.1">2.1 m</option>
                                                    <option value="2.2">2.2 m</option>
                                                    <option value="2.3">2.3 m</option>
                                                    <option value="2.4orAbove">2.4 m or above</option>
                                                </select>
                                                <div className="sep-10"></div>
                                            </li>
                                        }
                                    </div>
                                </ul>

                                <div className="sep-20"></div>

                                <div className="fifty left">
                                    <h5>Type of Booking</h5>
                                    <ul className="booking-options clearfix">
                                        <div className="oneThird">
                                            <li className={(this.props.filter.bookingType === "hourly" ? "active" : "")}
                                                onClick={(e) => this.props.handleFilterSelect('bookingType', 'hourly')}>
                                                <div className={"icon hourly " + (this.props.filter.bookingType === "hourly" ? "active" : "")}></div>
                                                <h5>Hourly</h5>
                                            </li>
                                        </div>
                                        <div className="oneThird">
                                            <li className={(this.props.filter.bookingType === "monthly" ? "active" : "")}
                                                onClick={(e) => this.props.handleFilterSelect('bookingType', 'monthly')}>
                                                <div className={"icon monthly " + (this.props.filter.bookingType === "monthly" ? "active" : "")}></div>
                                                <h5>Monthly</h5>
                                            </li>
                                        </div>
                                        <div className="oneThird">
                                            <li className={(this.props.filter.bookingType === "timeshare" ? "active" : "")}
                                                onClick={(e) => this.props.handleFilterSelect('bookingType', 'timeshare')}>
                                                <div className={"icon timeShare " + (this.props.filter.bookingType === "timeshare" ? "active" : "")}></div>
                                                <h5>Time Share</h5>
                                            </li>
                                        </div>
                                    </ul>
                                </div>

                                <div className="fifty left">
                                    <h5>Booking Type</h5>
                                    <ul className="booking-options clearfix">
                                        <div className="oneThird" >
                                            <li className={(this.props.filter.instant ? "active" : "")}
                                                onClick={(e) => this.props.handleFilterBoolean('instant')}>
                                                <div className={"icon timeShare " + (this.props.filter.instant ? "active" : "")}></div>
                                                <h5>Instant Booking</h5>
                                            </li>
                                        </div>
                                    </ul>
                                </div>

                                <div className="sep-20"></div>

                                <div className="fifty left">
                                    <h5>Walking Distance</h5>
                                    <div className="inputRangeDiv">
                                        <InputRange
                                            maxValue={30}
                                            minValue={0}
                                            value={this.props.filter.distance}
                                            formatLabel={value => `${value} mins`}
                                            onChange={value => this.props.handleFilterRange('distance', value)} />
                                    </div>
                                </div>

                                <div className="fifty left">
                                    <h5>Price</h5>
                                    <div className="inputRangeDiv">
                                        <InputRange
                                            maxValue={this.props.filter.initPrice.max}
                                            minValue={this.props.filter.initPrice.min}
                                            value={this.props.filter.price}
                                            formatLabel={value => `$${value}`}
                                            onChange={value => this.props.handleFilterRange('price', value)} />
                                    </div>
                                </div>

                                <div className="sep-40"></div>

                                <div className="wrap clearfix">
                                    <div className="fifty left">
                                        <div>
                                            <span className="button grey" onClick={(e) => this.props.handleReset()}>Reset</span>
                                        </div>
                                    </div>
                                    <div className="fifty left">
                                        <div>
                                            <span className="button" onClick={(e) => this.props.handleApplyFilter()}>Apply</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </ul>
                    </li>

                    <div className="sep-20"></div>
                </ul>
            </div>
        )
    }
}

// const mappropsToProps = (props) => (
// 	{
// 		route: props.router,
// 	}
// );

export default SortbyFilterDesktop2;
