import React, { Component } from 'react';
import { Tab, Tabs } from "react-bootstrap";
import { Accordion, AccordionItem, AccordionItemBody, AccordionItemTitle } from "react-accessible-accordion";
import FaqData from '../../data/helpCenter';
// import ShowMore from '../../components/103Parts/ShowMore';
import { withTranslation } from 'react-i18next';

function FaqBlock(props) {
    return (
        <AccordionItem>
            <AccordionItemTitle>
                <h3>{props.QuestionTitle}</h3>
            </AccordionItemTitle>
            <AccordionItemBody dangerouslySetInnerHTML={{ __html: props.QuestionAnswer }}>
            </AccordionItemBody>
        </AccordionItem>
    )
}

class HelpCenter extends Component {
    constructor(props) {
        super(props)
        this.state = {
            Faq: {
                ParkersFaq: [],
                OwnersFaq: [],
                GeneralFaq: [],
            },
            TabInitDisplay: 1,
        };
    }

    componentDidMount = () => {
        this.getFAQ();
        this.setTabsDefault();
    }

    getFAQ = () => {

        let sampleFAQ = FaqData;

        this.setState({
            ...this.state,
            Faq: {
                ParkersFaq: sampleFAQ.Parkers,
                OwnersFaq: sampleFAQ.Owners,
                GeneralFaq: sampleFAQ.General,
            }
        });
    }

    setTabsDefault = () => {

        let keys = Object.keys(this.getUrlVars());

        for (let i = 0; i < keys.length; i++) {

            let key = (keys[i]);

            if (this.getUrlVars()[key].session === 'parkers') {

                this.setState({
                    TabInitDisplay: 1,
                });

                break;
            } else if (this.getUrlVars()[key].session === 'owners') {

                this.setState({
                    TabInitDisplay: 2,
                });

                break;
            } else {

                this.setState({
                    TabInitDisplay: 1,
                });
            }
        }
    }

    getUrlVars = () => {

        let vars = [];

        window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi, function (m, key, value) {

            let each = {};

            each[key] = value;

            vars.push(each);
        });

        return vars;
    }

    handleTabSelect = (data) => {
        this.setState({
            TabInitDisplay: data
        })
    }

    render() {
        const {
            t,
            //i18n 
        } = this.props;

        return (
            <div className="wrapper-container helpCenter">
                <div className="page-title"><h2>{t("Menu.HelpCentre")}</h2></div>
                <div className="sep-0"></div>
                <div className="containerMain">
                    <div className="content clearfix">
                        <div className="wrapper-tabs">
                            <Tabs activeKey={this.state.TabInitDisplay} onSelect={(e) => this.handleTabSelect(e)} id="uncontrolled-tab-example">
                                <Tab eventKey={1} title={t("helpCenter:TabTitle.Parkers")}>
                                    <div className="inner">
                                        <Accordion>
                                            {this.state.Faq.ParkersFaq.map((value, key) => {

                                                return (
                                                    <FaqBlock
                                                        key={key}
                                                        QuestionTitle={value.QuestionTitle}
                                                        QuestionAnswer={value.QuestionAnswer}
                                                    />
                                                )
                                            })}
                                        </Accordion>

                                        {/*<ShowMore />*/}
                                    </div>
                                </Tab>
                                <Tab eventKey={2} title={t("helpCenter:TabTitle.Owners")}>
                                    <div className="inner">
                                        <Accordion>
                                            {this.state.Faq.OwnersFaq.map((value, key) => {

                                                return (
                                                    <FaqBlock
                                                        key={key}
                                                        QuestionTitle={value.QuestionTitle}
                                                        QuestionAnswer={value.QuestionAnswer}
                                                    />
                                                )
                                            })}
                                        </Accordion>

                                        {/*<ShowMore />*/}
                                    </div>
                                </Tab>
                                <Tab eventKey={3} title={t("helpCenter:TabTitle.General")}>
                                    <div className="inner">
                                        <Accordion>
                                            {this.state.Faq.GeneralFaq.map((value, key) => {

                                                return (
                                                    <FaqBlock
                                                        key={key}
                                                        QuestionTitle={value.QuestionTitle}
                                                        QuestionAnswer={value.QuestionAnswer}
                                                    />
                                                )
                                            })}
                                        </Accordion>

                                        {/*<ShowMore />*/}
                                    </div>
                                </Tab>
                            </Tabs>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default withTranslation()(HelpCenter);
