import React, { Component } from "react";
import { withTranslation } from 'react-i18next';
// import $ from 'jquery';

import ViewChangeButton from '../../components/03FindParkingSpace/ViewChangeButton';
import ClusterMap from "../../components/103Parts/ClusterMap";
import ListType2b from '../../components/102Grids/ListType2b';

// dummy data
import genMarkersData from "../../data/genMarkersData";

class Block extends Component {
    render() {
        return (
            <ListType2b
                id={this.props.id}
                count={this.props.count}
                space={this.props.space}
                verified={this.props.verified}
                wishlist={this.props.wishlist}
                handleWishlist={this.props.handleWishlist}
                price={this.props.price}
                carTypes={this.props.carTypes}
                score={this.props.score}
                reviews={this.props.reviews}
                image={this.props.image}
            />
        )
    }
}

class FindYourSpaceMapView extends Component {

    render() {
        return (
            <div className="containerMain fullWidth mapView">
                <ViewChangeButton
                    showView={this.props.showView}
                    handleViewChange={this.props.handleViewChange}
                />
                <ClusterMap
                    markers={genMarkersData({
                        count: 100,
                        seed: 7,
                        latVarM: 0.5,
                        lngVarM: 0.5,
                        test: true
                    })}
                    onMarkerClicked={marker =>
                        console.log("clicking ", marker.get("id"))
                    }
                />
                <div className="sep-0"></div>

                <div className="wrapper clearfix">

                    {this.props.spaces
                        // .filter(guest => !props.isFiltered || guest.isConfirmed)
                        .map((space, i) =>
                            <div className="full left" key={i}>
                                <Block
                                    key={i}
                                    id={i}
                                    count={i}
                                    space={space}
                                    verified={space.verified}
                                    wishlist={this.props.wishlist}
                                    handleWishlist={this.props.handleWishlist}
                                    price={space.price}
                                    carTypes={space.carTypes}
                                    score={space.score}
                                    reviews={space.reviews}
                                    image={space.image}
                                />
                            </div>
                        )}
                </div>
            </div>
        );
    }
}

export default withTranslation()(FindYourSpaceMapView);
