import React from 'react';
import { Redirect } from 'react-router';
import { withTranslation } from 'react-i18next';

// import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
// import * as findParkingSpaceActionCreators from '../../actions/findParkingSpace';

function ListASpaceConfirm({ t, i18n }) {
    if (this.props.members.Login.length === 0) {
        return <Redirect to={"/" + i18n.language + "/memberLogin"} />;
    }

    return (
        <div className="wrapper-container confirmation padding" >
            <div className="containerMain">
                <div className="content clearfix">
                    <h3 className="align-center">Your parking space is pending for approval.</h3>

                    <p className="align-center">Thank you for choosing Let’s Park!</p>
                </div>
            </div>
        </div>
    );
}

const mapStateToProps = state => (
    {
        members: state.members,
    }
);

export default withTranslation()(connect(mapStateToProps)(ListASpaceConfirm));
