import React, { Component } from 'react';
import { Tabs, Tab } from 'react-bootstrap';

import ListType4 from '../../components/102Grids/ListType4';
import ShowMore from '../../components/103Parts/ShowMore';

//import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
//import * as MessagesActionCreators from '../../actions/messages';

class Block extends Component {
	render() {
		return (
			<ListType4 count={this.props.count} />
		)
	}
}

class Cluster extends Component {
	render() {
		let rows = [];
		for (let i = 0; i < 6; i++) {
			rows.push(
				<div className="full left" key={i}>
					<Block count={i} />
				</div>
			)
		}
		return (rows);
	}
}

class Messenger extends Component {

	constructor(props) {

		super(props);

		// const { dispatch } = props;

		// DISPATCH TO ACTIONS

		this.state = {
			score: 3,
			reviews: 120,
			carTypes: ['Indoor', 'EV'],
			PopupContent: 'MemberSettingsBookings'
		}
	}

	render() {

		//const { messages,
		//translate
		//} = this.props;

		return (
			<div className="wrapper-container messages">
				<div className="containerMain">

					<div className="content clearfix">
						<div className="wrapper-tabs clearfix">
							<Tabs defaultActiveKey={1} id="uncontrolled-tab-example">
								<Tab eventKey={1} title="Message">
									<Cluster />
								</Tab>
								<Tab eventKey={2} title="Notifications">
									<Cluster />
								</Tab>
							</Tabs>
						</div>
					</div>

					<ShowMore />
				</div>
			</div>
		);
	}
}

const mapStateToProps = (state) => (
	{
		messages: state.messages
	}
);

export default connect(mapStateToProps)(Messenger);
