import React, { Component } from 'react';

class Landlord extends Component {
    render() {
        return (
            <div className="spaceInfo clearfix">
                <div className="person right">
                    <div className="image">
                        <img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" />
                    </div>

                    <h3>Max Cheung</h3>

                    <div className="memberSince">Member Since <span>Mar 29, 2017</span></div>
                </div>
                <div className="space left">
                    <h3>Causeway Bay, COFCO Tower</h3>
                    <h4>262 Cloucester Road, Causeway Bay</h4>
                    <span className="spaceId">Space ID - S005682</span>
                </div>
            </div>
        )
    }
}

export default Landlord;
