import * as MessagesActionTypes from '../actiontypes/messages';

export const handleSocket = (message) => {
	return {
		type: MessagesActionTypes.HANDLE_SOCKET,
		meta: {remote: true},
		message
	};
};

export const pendingMessage = (message) => {
	return {
		type: MessagesActionTypes.PENDING_MESSAGE,
		message
	};
};

export const addMessage = (message) => {
	return {
		type: MessagesActionTypes.ADD_MESSAGE,
		message
	};
};
