import React from 'react';
import { Link } from 'react-router-dom';
import { withTranslation } from 'react-i18next';

import MemberMenuDesktop from '../../../components/02Members/MemberMenuDesktop';
import MemberMenuMobile from '../../../components/02Members/MemberMenuMobile';

// import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
// import * as MembersActionCreators from '../../actions/members';

function MemberCreateAccount(props) {
    // const {t} = props;
    return (
        <div className="wrapper-container">
            <MemberMenuMobile />
            <div className="containerMain">
                <MemberMenuDesktop />

                <div className="content clearfix">
                    <dl className="generalForm clearfix">
                        <div>
                            <dt><h4>Profile Pic</h4></dt>
                            <dd></dd>
                        </div>

                        <div className="sep-25"></div>

                        <div>
                            <dt><h4>Personal Details *</h4></dt>
                            <dd>
                                <input type="text" placeholder="" />
                            </dd>
                        </div>

                        <div className="sep-25"></div>

                        <div>
                            <dt></dt>
                            <dd>
                                <div className="wrap clearfix">
                                    <div className="fifty">
                                        <div>
                                            <input type="text" placeholder="" />
                                        </div>
                                    </div>
                                    <div className="fifty">
                                        <div>
                                            <input type="text" placeholder="" />
                                        </div>
                                    </div>
                                </div>
                            </dd>
                        </div>

                        <div className="sep-25"></div>

                        <div>
                            <dt><h4>Name in HKID</h4></dt>
                            <dd>
                                <input type="text" placeholder="" />
                            </dd>
                        </div>

                        <div className="sep-25"></div>

                        <div>
                            <dt><h4>Email Address *</h4></dt>
                            <dd>
                                <input type="text" placeholder="" />
                            </dd>
                        </div>

                        <div className="sep-25"></div>

                        <div>
                            <dt><h4>Mobile Number *</h4></dt>
                            <dd>
                                <input type="text" placeholder="" />
                            </dd>
                        </div>

                        <div className="sep-25"></div>

                        <div>
                            <dt></dt>
                            <dd>
                                <div className="wrap clearfix">
                                    <div className="fifty">
                                        <div>
                                            <input type="text" placeholder="" />
                                        </div>
                                    </div>
                                    <div className="fifty">
                                        <div>
                                            <input type="text" placeholder="" />
                                        </div>
                                    </div>
                                </div>
                            </dd>
                        </div>

                        <div className="sep-25"></div>

                        <div>
                            <dt><h4>Date of Birth</h4></dt>
                            <dd>
                                <div className="wrap clearfix">
                                    <div className="fifty">
                                        <div>
                                            <Link to="/" className="button">Send out the code</Link>
                                        </div>
                                    </div>
                                    <div className="fifty">
                                        <div>
                                            <input type="text" placeholder="SMS Verification Code" />
                                        </div>
                                    </div>
                                </div>
                            </dd>
                        </div>

                        <div className="sep-25"></div>

                        <div>
                            <dt><h4>Vehicle Registration Mark</h4></dt>
                            <dd>
                                <input type="text" placeholder="" />
                            </dd>
                        </div>
                    </dl>

                    <div className="sep-25"></div>
                    <div className="sep-25"></div>

                    <div className="full left align-center">
                        <div>
                            <input type="submit" className="button" value="Save" />
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

const mapStateToProps = (state) => (
    {
        members: state.members
    }
);

export default withTranslation()(connect(mapStateToProps)(MemberCreateAccount));
