import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Redirect } from 'react-router';
import { Tabs, Tab } from 'react-bootstrap';
import { withTranslation } from 'react-i18next';

import moment from 'moment';

import ViewChangeWithSlider from "../../components/03FindParkingSpace/ViewChangeWithSlider";
import { lowestPrice } from "../../utils/03FindParkingSpace/LowestHourlyPrice";

import Tags from '../../components/103Parts/Tags';
import SpaceSuitableFor from '../../components/103Parts/SpaceSuitableFor';
import Features from '../../components/103Parts/Features';
import Restrictions from '../../components/103Parts/Restrictions';
import AccessType from '../../components/103Parts/AccessType';
import PrevNext from '../../components/04ListASpace/PrevNext';

import * as Config from '../../config';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as ListParkingSpaceCreators from '../../actions/listParkingSpace';

function BookNow(props) {
	return (
		<div className="book left">
			<hr />

			<div className="sep-15"></div>

			<div className="price">From HKD <span>{lowestPrice(props.hourlyDays)}</span> per hour <div className="instantBooking"></div></div>

			<div className="sep-15"></div>

			<hr />

			<div className="sep-15"></div>

			<table className="verifiedGuarantee">
				<tbody>
					<tr>
						<td> <img src={require('../../images/mobile/03_find_parking_space/icon_guarantee.png')} alt="" /></td>
						<td>Land search <span>Verified</span></td>
					</tr>
					<tr>
						<td> <img src={require('../../images/mobile/03_find_parking_space/icon_verified.png')} alt="" /></td>
						<td>You are always protected by our Parker Protection <span>Guarantee</span></td>
					</tr>
				</tbody>
			</table>

			<div className="sep-15"></div>

			<Link to="/" className="button bookNow"
				onClick={(e) => e.preventDefault()}
			>Book Now</Link>
		</div>
	);
}

class Preview extends Component {
	constructor(props) {
		super(props);
		const { dispatch } = props;

		// DISPATCH TO ACTIONS
		this.ClearListParkingSpace = bindActionCreators(ListParkingSpaceCreators.ClearListParkingSpace, dispatch);

		this.state = ({
			view: 'photo',
			formSubmitted: false,
			wishlist: {
				1: true
			},
			data: {
				accesstype: [],
				address: "",
				booked: [],
				cartype: [],
				description: [],
				features: [],
				floor: "",
				height: "",
				hourly: {
					always: true,
					available: [],
					avatype: [],
					blocked: [],
					days: [],
					deposit: 0,
					from: "",
					minbooking: 1,
					to: ""
				},
				images: [],
				instant: true,
				lat: "",
				lng: "",
				monthly: {
					blocked: [],
					from: "",
					minbooking: 1,
					price: 0,
					to: "",
				},
				owner: [],
				restrictions: [],
				spaceno: "",
				timeshare: {
					available: [],
					avatype: [],
					blocked: [],
					from: "",
					minbooking: 1,
					price: 0,
					to: "",
				},
				title: "",
			}
		})
	}

	componentDidMount = () => {
		this.GetPreviousData();
	}

	// General
	handleChangeView = (e, data1, data2) => {
		e.preventDefault();
		this.setState({
			...this.state,
			[data1]: data2
		})
	}

	handleWishlist = (id) => {
		this.setState({
			wishlist: {
				...this.state.wishlist,
				[id]: !this.state.wishlist[id]
			}
		});
	}

	GetPreviousData = () => {
		var stepOne = this.props.listParkingSpace.StepOne,
			stepTwo = this.props.listParkingSpace.StepTwo,
			stepThree = this.props.listParkingSpace.StepThree

		var Body = {
			"title": stepOne.addressTitle,
			"address": stepOne.address,
			"floor": stepOne.floor,
			"spaceno": stepOne.spaceNo,
			"lng": stepOne.lng,
			"lat": stepOne.lat,
			"cartype": [],
			"features": [stepOne.Feature],
			"height": "",
			"restrictions": [stepOne.Restrictions],
			"description": [],
			"accesstype": [stepOne.AccessType],
			"instant": !!stepTwo.InstantBooking,
			"images": [],
			"owner": [stepThree.ownerName, stepThree.ownerName2],
			"monthly": {},
			"timeshare": {},
			"hourly": {},
		};

		var carTypes = ["PrivateCar", "Van", "Motorcycle", "Lorry", "Others"];
		for (let i = 0; i < carTypes.length; i++) {
			let carType = carTypes[i];
			if (stepOne.SpaceSuitable[carType])
				Body.cartype.push(carType);
		}

		if (stepOne.FeatureEV.yes)
			Body.features.push("EV");

		if (stepOne.FeatureHeadRoom.yes) {
			Body.features.push("HeadRoom");
			Body.height = stepOne.FeatureHeadRoom.Height;
		}

		var descriptions = ["NearEntrance", "WideLoad", "NearLift", "NearStairs", "NearBlock", "NearBlockText", "CorneredSpace"];
		for (let i = 0; i < descriptions.length; i++) {
			let description = descriptions[i];
			if (stepOne.DetailedDescription[description])
				Body.description.push(description);
		}

		// Hourly Data
		if (stepTwo.BookingType.Hourly) {
			// support hourly
			let tab = stepTwo.HourlyTab,
				availability = tab.Availability;
			let booking_type_data = {
				"from": moment(availability.SelectedDateRange.DateStart).utcOffset(8).format('YYYY-MM-DD'),
				"to": moment(availability.SelectedDateRange.DateEnd).utcOffset(8).format('YYYY-MM-DD'),
				"min": tab.Pricing.minBooking,
				"type": [],
				"always": availability.AlwaysAvailable !== "No",
				"deposit": 0,
				"days": [],
				"available": [],
				"blocked": [],
			};

			if (stepOne.AccessType !== "No") {
				booking_type_data.deposit = tab.Pricing.deposit;
			}


			for (let i = 0; i < availability.BlockedDateRange.length; i++) {
				let range = availability.BlockedDateRange[i];
				if (range.DateStart !== "" && range.DateEnd !== "") {
					booking_type_data.blocked.push({
						"from": moment(range.DateStart).utcOffset(8).format('YYYY-MM-DD'),
						"to": moment(range.DateEnd).utcOffset(8).format('YYYY-MM-DD'),
					});
				}
			}

			if (availability.AlwaysAvailable !== "No") {
				// always available
				let price = availability.Price[0],
					days = [1, 2, 3, 4, 5, 6, 7, 8];

				booking_type_data.available.push({
					"period": "MonSunPH",
					"stime": "00:00",
					"etime": "24:00",
					"price": price,
				});

				for (let i = 0; i < days.length; i++) {
					let day = days[i];
					booking_type_data.days.push({
						"day": day,
						"stime": "00:00",
						"etime": "24:00",
						"price": price,
					});
				}
			} else {
				for (let i = 0; i < availability.AvailableDayTime.length; i++) {
					let availableTime = availability.AvailableDayTime[i];
					booking_type_data.type.push(availableTime.SelectedDayOfWeek);
					let price = availability.Price[i];

					switch (availableTime.SelectedDayOfWeek) {
						case "Custom":
							let days = availableTime.CustomDates;
							for (let j = 0; j < days.length; j++) {
								let day = days[j];
								booking_type_data.available.push({
									"period": day,
									"stime": availableTime.SelectedTimeRange.TimeStart,
									"etime": availableTime.SelectedTimeRange.TimeEnd,
									"price": price,
								});

								let theday;
								switch (day) {
									case "Mon":
										theday = 1;
										break;
									case "Tue":
										theday = 2;
										break;
									case "Wed":
										theday = 3;
										break;
									case "Thu":
										theday = 4;
										break;
									case "Fri":
										theday = 5;
										break;
									case "Sat":
										theday = 6;
										break;
									default:
										theday = 7;
										break;
								}

								booking_type_data.days.push({
									"day": theday,
									"stime": availableTime.SelectedTimeRange.TimeStart,
									"etime": availableTime.SelectedTimeRange.TimeEnd,
									"price": price,
								});
							}
							break;
						default:
							booking_type_data.available.push({
								"period": availableTime.SelectedDayOfWeek,
								"stime": availableTime.SelectedTimeRange.TimeStart,
								"etime": availableTime.SelectedTimeRange.TimeEnd,
								"price": price,
							});

							let availDays;
							switch (availableTime.SelectedDayOfWeek) {
								case "SatSunPH":
									availDays = [6, 7, 8];
									break;
								case "MonFri":
									availDays = [1, 2, 3, 4, 5];
									break;
								default:
									availDays = [1, 2, 3, 4, 5, 6, 7, 8];
									break;
							}
							for (let i = 0; i < availDays.length; i++) {
								let day = availDays[i];
								booking_type_data.days.push({
									"day": day,
									"stime": availableTime.SelectedTimeRange.TimeStart,
									"etime": availableTime.SelectedTimeRange.TimeEnd,
									"price": price,
								});
							}
					}
				}
			}

			for (let i = 0; i < tab.CalendarData.CustomDates.length; i++) {
				let d = tab.CalendarData.CustomDates[i];
				if (!d.Available) {
					const theDate = moment(d.Date).utcOffset(8).format('YYYY-MM-DD');
					booking_type_data.blocked.push({
						"from": theDate,
						"to": theDate,
					});
				}
			}

			Body.hourly = booking_type_data;
		}

		// Monthly Data
		if (stepTwo.BookingType.Monthly) {
			// supports Monthly
			let tab = stepTwo.MonthlyTab,
				availability = tab.Availability;
			let booking_type_data = {
				"mfrom": moment(availability.SelectedDateRange.DateStart).utcOffset(8).format('YYYY-MM-DD'),
				"mto": moment(availability.SelectedDateRange.DateEnd).utcOffset(8).format('YYYY-MM-DD'),
				"min": tab.Pricing.minBooking,
				"blocked": [],
				"mprice": tab.Pricing.price,
			};

			for (let i = 0; i < availability.BlockedDateRange.length; i++) {
				let range = availability.BlockedDateRange[i];
				if (range.DateStart !== "" && range.DateEnd !== "") {
					booking_type_data.blocked.push({
						"from": moment(range.DateStart).utcOffset(8).format('YYYY-MM-DD'),
						"to": moment(range.DateEnd).utcOffset(8).format('YYYY-MM-DD'),
					});
				}
			}
			Body.monthly = booking_type_data;
		}

		// Timeshare Data
		if (stepTwo.BookingType.Timeshare) {
			// support Timeshare
			let tab = stepTwo.TimeshareTab,
				availability = tab.Availability;
			let booking_type_data = {
				"from": moment(availability.SelectedDateRange.DateStart).utcOffset(8).format('YYYY-MM-DD'),
				"to": moment(availability.SelectedDateRange.DateEnd).utcOffset(8).format('YYYY-MM-DD'),
				"min": tab.Pricing.minBooking,
				"type": [],
				"available": [],
				"blocked": [],
				"price": tab.Pricing.price,
			};

			for (let i = 0; i < availability.BlockedDateRange.length; i++) {
				let range = availability.BlockedDateRange[i];
				if (range.DateStart !== "" && range.DateEnd !== "") {
					booking_type_data.blocked.push({
						"from": moment(range.DateStart).utcOffset(8).format('YYYY-MM-DD'),
						"to": moment(range.DateEnd).utcOffset(8).format('YYYY-MM-DD'),
					});
				}
			}

			for (let i = 0; i < availability.AvailableDayTime.length; i++) {
				let availableTime = availability.AvailableDayTime[i];
				booking_type_data.type.push(availableTime.SelectedDayOfWeek);
				switch (availableTime.SelectedDayOfWeek) {
					case "Custom":
						let days = availableTime.CustomDates;
						for (let j = 0; j < days.length; j++) {
							let day = days[j];
							booking_type_data.available.push({
								"period": day,
								"stime": availableTime.SelectedTimeRange.TimeStart,
								"etime": availableTime.SelectedTimeRange.TimeEnd,
							});
						}
						break;
					default:
						booking_type_data.available.push({
							"period": availableTime.SelectedDayOfWeek,
							"stime": availableTime.SelectedTimeRange.TimeStart,
							"etime": availableTime.SelectedTimeRange.TimeEnd,
						});
				}
			}

			Body.timeshare = booking_type_data;
		}

		this.setState({
			...this.state,
			data: Body
		});
	}

	submitForm = (e) => {
		e.preventDefault();

		this.ApiListYourSpace();
	};

	ApiListYourSpace = () => {
		let access_token = this.props.members.Login.userToken;
		fetch(Config.API_URL + 'space?access_token=' + access_token, {
			method: 'post',
			headers: new Headers({
				//'Authorization': 'Basic ' + btoa(this.props.members.Login.userToken + ':' + Config.UAT_FEED_PASSWORD),
				'Accept': 'application/json',
				'Content-Type': 'application/json'
			}),
			body: JSON.stringify(this.state.data)
		})
			// .then(r => r.json().then(data => ({ status: r.status, body: data })
			// ))
			.then(obj => {
				if (obj.status === 200) {
					this.goToConfirm(this.state.data);
				} else if (obj.status === 400) {
					this.setState({
						errors: obj.body.messages
					});
				}

			}).catch(error => {
				console.log("error");
				console.log(error)
			});
	}

	goToConfirm = () => {
		this.setState({
			...this.state,
			formSubmitted: true
		}, this.ClearListParkingSpace);
	}

	render() {
		const {
			// t,
			i18n
		} = this.props;

		if (this.props.members.Login.length === 0) {
			return <Redirect to={"/" + i18n.language + "/memberLogin"} />;
		}

		if (this.props.listParkingSpace.StepOne.length === 0) {
			return <Redirect to={"/" + i18n.language + "/stepOne"} />;
		}

		if (this.props.listParkingSpace.StepTwo.length === 0) {
			return <Redirect to={"/" + i18n.language + "/stepTwo"} />;
		}

		if (this.props.listParkingSpace.StepThree.length === 0) {
			return <Redirect to={"/" + i18n.language + "/stepThree"} />;
		}

		if (this.state.formSubmitted) {
			return <Redirect to={"/" + i18n.language + "/listASpaceConfirm"} />;
		}

		return (
			<div className="wrapper-container detailShare">
				<div className="containerMain">
					<div className="content clearfix">
						<div className="previewBg">
							<div className="information left">
								<div className="text">
									<h3>{this.state.data.title}</h3>
									<h4>{this.state.data.address}</h4>
									{/* <div className="sep-0"></div>
									<Reviews reviews={this.state.reviews} score={this.state.score} /> */}

									<div className="sep-15"></div>

									<Tags
										tags={this.state.data.description}
									/>

									<div className="sep-20"></div>
								</div>
								<ViewChangeWithSlider
									view={this.state.view}
									handleChangeView={this.handleChangeView}
									wishlist={this.state.wishlist}
									handleWishlist={this.handleWishlist}
									lng={this.state.data.lng}
									lat={this.state.data.lat}
								/>
								<div className="sep-40"></div>

								<div className="landlord clearfix">

									<div className="fifty clearfix">

										<div className="image">
											<img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" />
										</div>
										<div className="text">
											<h3>{this.state.data.owner[0]}</h3>
											<div className="memberSince">Member Since <span>Mar 29, 2017</span></div>
											<div className="sep-10"></div>
											<a href="/" className="button"
												onClick={(e) => e.preventDefault()}>Contact Owner</a>
										</div>
									</div>

									<div className="fifty clearfix"></div>
								</div>
							</div>

							{(!this.state.pressedBookNow) &&
								<BookNow
									handleBookNow={this.handleBookNow}
									hourlyDays={this.state.data.hourly.days}
								/>
							}

							<div className="sep-30"></div>

							<div className="wrapper-tabs">
								<Tabs defaultActiveKey={1} id="uncontrolled-tab-example">
									<Tab eventKey={1} title="Details">
										<div className="inner">
											<h5>Space suitable for</h5>

											<SpaceSuitableFor
												cartype={this.state.data.cartype}
											/>


											<div className="sep-25"></div>

											<h5>Feature</h5>
											<Features
												features={this.state.data.features}
											/>
											<div className="sep-25"></div>

											<h5>Restrictions</h5>
											<Restrictions
												restrictions={this.state.data.restrictions[0]}
											/>
											<div className="sep-25"></div>

											<h5>Access Type</h5>
											<AccessType
												accesstype={this.state.data.accesstype[0]}
											/>
										</div>
									</Tab>
									<Tab eventKey={2} title="Reviews">
										<div className="inner">
											No Review Yet
										</div>
									</Tab>
								</Tabs>
							</div>

							<div className="sep-25"></div>
							<div className="previewOverlay"></div>
						</div>

						<PrevNext
							prevHas={true}
							prevURL={"/" + i18n.language + "/stepThree"}
							prevText='Back'
							nextURL={"/" + i18n.language + "/listASpaceConfirm"}
							nextText='List'
							progress="three"
							value="3/3"
							prevHandler={null}
							nextHandler={(e) => this.submitForm(e)}
						/>

					</div>
				</div>
			</div>
		);
	}
}

const mapStateToProps = (state) => (
	{
		members: state.members,
		listParkingSpace: state.listParkingSpace
	}
);

export default withTranslation()(connect(mapStateToProps)(Preview));
