import React, { Component } from 'react';
import { withTranslation } from 'react-i18next';
// import { Redirect } from 'react-router';
import { Link } from 'react-router-dom';

import DatePicker from "react-datepicker";
import moment from "moment";

import * as Helper from "../../utils/03FindParkingSpace/Calculations";

// import { bindActionCreators } from 'redux';
// import { connect } from 'react-redux';
// import * as FindParkingSpaceActionCreators from '../../actions/findParkingSpace';

class Payment extends Component {
	render() {
		return (
			<div>
				<Link to="/" className={this.props.className + " button bookNow"} onClick={(e) => {e.preventDefault(); this.props.goToPayment(e)}}>Looks Great, Let's Go</Link>

				<div className="sep-15"></div>

				<p className="notChargedYet">You won't be charged yet</p>
			</div>
		)
	}
}

class Total extends Component {
	render() {
		return (
			<dl className="clearfix">
				{/*<dt>HKD 20 x {this.props.calculation.hours} hour(s)</dt>*/}
				{/*<dd>HKD {Helper.number_format(this.props.calculation.amount)}</dd>*/}
				{/*<div className="sep-0"></div>*/}
				<dt><b>Total Amount</b></dt>
				<dd><b>HKD  {Helper.number_format(this.props.calculation.total, 0)}</b></dd>


				{/*<dt>{this.props.calculation.startDate} - {this.props.calculation.endDate}</dt>*/}
				{/*<dd>HKD {Helper.number_format(this.props.calculation.amount)}</dd>*/}
				{/*<div className="sep-0"></div>*/}
				{/*<dt>Deposit</dt>*/}
				{/*<dd>HKD  {Helper.number_format(this.props.calculation.deposit)}</dd>*/}
				{/*<div className="sep-0"></div>*/}
				{/*<dt><b>Total Amount</b></dt>*/}
				{/*<dd><b>HKD  {Helper.number_format(this.props.calculation.total)}</b></dd>*/}
			</dl>
		)
	}
}

class DetailTimeshare extends Component {
	onDatepickerRef = (el) => {
		if (el && el.input) {
			el.input.readOnly = true;
		}
	}

	render() {

		// const { findParkingSpace,
		// 	//translate
		// } = this.props;

		// if (this.state.formSubmit) {
		// 	return <Redirect to={"/" + i18n.language + "/payment"} />;
		// }

		const booking_types = {
			'hourly': 'Hourly',
			'monthly': 'Monthly',
			'timeshare': 'Timeshare',
		}
		const supportTypeSelect = this.props.supportTypes.map((t, index) =>
			<option value={t} key={index}>{booking_types[t]}</option>
		);

		let availability = null;
		if (typeof (this.props.calculation.success) === "boolean") {

			if (this.props.calculation.success)
				availability = <Total calculation={this.props.calculation} />
			else
				availability = <div>Sorry, time not available.</div>
		}

		return (
			<div className="book left">
				<select value={this.props.BookingType}
					onChange={(e) => this.props.handleBookingType(e.target.value)}>
					{supportTypeSelect}
				</select>

				<div className="sep-20"></div>

				<div className="price">
					HKD <span>{this.props.typeDetail.price}</span> per month
				</div>

				<div className="sep-20"></div>
				{false &&
					<div>
						<div className="radioFive">
							<div>
								<input type="radio" id="non-reserve3" name="reserveTimeshare" value="3600"
									checked={!this.props.reserved}
									onChange={(e) => this.props.handleReserved('timeshare', false)}
								/>
								<label htmlFor="non-reserve3"></label>
							</div>
							<label htmlFor="non-reserve3">Non-reserved : HKD <span>3,600</span> per month</label>
						</div>

						<div className="sep-10"></div>

						<div className="radioFive">
							<div>
								<input type="radio" id="reserve4" name="reserveTimeshare" value="4000"
									checked={this.props.reserved}
									onChange={(e) => this.props.handleReserved('timeshare', true)}
								/>
								<label htmlFor="reserve4"></label>
							</div>
							<label htmlFor="reserve4">Reserved : HKD <span>4,000</span> per month</label>
						</div>

						{/* <input type="radio" />  */}
						{/* <div className="sep-10"></div> */}
						{/* <input type="radio" /> Reserved : HKD <span>2,300</span> per month */}

						<div className="sep-20"></div>
					</div>
				}

				<DatePicker
					dateFormat="YYYY-MM-DD"
					selected={this.props.startDate === "" ? null : this.props.startDate}
					onChange={(e) => this.props.updateState('toBookTimeshare', 'startDate', e)}
					minDate={moment()}
					maxDate={!this.props.endDate || this.props.endDate === "" ? null : moment(this.props.endDate).subtract(1, 'month')}
					placeholderText="Start Date"
					ref={el => this.onDatepickerRef(el)}
				/>

				<div className="sep-15"></div>

				<DatePicker
					dateFormat="YYYY-MM-DD"
					selected={this.props.endDate === "" ? null : this.props.endDate}
					onChange={(e) => this.props.updateState('toBookTimeshare', 'endDate', e)}
					minDate={!this.props.startDate || this.props.startDate === "" ? moment().add(1, 'month') : moment(this.props.startDate).add(1, 'month')}
					placeholderText="End Date"
					ref={el => this.onDatepickerRef(el)}
				/>

				<div className="sep-20"></div>

				{availability}
				{/*{this.props.calculation.success ? <Total calculation={this.props.calculation} /> : ''}*/}

				<div className="sep-10"></div>

				<p className="remarks">
					{this.props.typeDetail.minbooking > 0 &&
					<span>Minimum {this.props.typeDetail.minbooking} months booking<br /></span>
					}
					Full calendar month booking (except the first month)
				</p>


				<div className="sep-20"></div>

				<table className="verifiedGuarantee">
					<tbody>
						<tr>
							<td><img src={require('../../images/mobile/03_find_parking_space/icon_verified.png')} alt="" /></td>
							<td>Land search <span>Verified</span></td>
						</tr>
						<tr>
							<td> <img src={require('../../images/mobile/03_find_parking_space/icon_guarantee.png')} alt="" /></td>
							<td>You are always protected by our Parker Protection <span>Guarantee</span></td>
						</tr>
					</tbody>
				</table>

				<div className="sep-20"></div>

				{this.props.calculation.success ?
					<Payment
						calculation={this.props.calculation}
						goToPayment={this.props.goToPayment}
					/> : ''}
			</div>
		);
	}
}

