class Privacy extends Component {
    render() {
        const {
            //t,
            i18n
        } = this.props;

        return (
            <div className="wrapper-container media">

                <div className="page-title"><h2>Privacy Policy</h2></div>

                <div className="sep-0"></div>

                <div className="containerMain">
                    <div className="content clearfix">
                        {i18n.language === 'zh-HK'
                            ? <PrivacyZHHK />
                            : <PrivacyENUS />
                        }
                    </div>
                </div>
            </div>
        );
    }
}

