import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Redirect } from 'react-router';
import { withTranslation } from 'react-i18next';

import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';

import * as Config from '../../../config';
// import * as Helper from '../../utils/02Members/Validations';
import ErrorMessage from '../../../components/103Parts/ErrorMessage';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as MembersActionCreators from "../../../actions/members";


// Form Validation
const Schema = Yup.object().shape({
    email: Yup.string()
        .email('Invalid email')
        .required('Email is Required'),
    password: Yup.string()
        .required('Password is Required'),
    // rememberMe: Yup.boolean()
    //     .required('Remember Me is Required')
    //     .oneOf([true], 'Must Remember Me'),
});

class MemberLogin extends Component {

    constructor(props) {

        super(props);

        const { dispatch } = props;

        // DISPATCH TO ACTIONS
        this.toLogin = bindActionCreators(MembersActionCreators.toLogin, dispatch);

        this.state = {
            errors: null,
        }

        this.ApiLogin = this.ApiLogin.bind(this);
    }

    ApiLogin = (data) => {
        this.setState({
            errors: null,
        }, function () {
            var body = {
                email: data.EmailAddress,
                passwd: data.Password
            }

            fetch(Config.API_URL + 'auth', {
                method: 'post',
                headers: new Headers({
                    // 'Authorization': 'Basic ' + btoa(Config.UAT_API_LOGIN + ':' + Config.UAT_API_PASSWORD),
                    'Accept': 'application/json',
                    'Content-Type': 'application/json'
                }),
                body: JSON.stringify(body)
            }).then(r => r.json().then(data => ({ status: r.status, body: data })
            )).then(obj => {

                if (obj.status === 200) {

                    const postData = {
                        userToken: obj.body.token
                    }

                    this.toLogin(postData);

                } else if (obj.status === 400) {

                    console.log(obj.body.msg)
                    this.setState({
                        errors: obj.body.msg
                    });
                }

            }).catch(error => {
                console.log(error)
            });
        });
    }

    render() {
        const {
            // t,
            i18n
        } = this.props;

        if (this.props.members.Login && this.props.members.Login.length !== 0) {
            //return <Redirect to={"/" + i18n.language + "/stepOne"} />;
            return <Redirect to={"/" + i18n.language + ""} />;
        }

        return (
            <div className="wrapper-container memberLogin padding">
                <div className="containerMain borderTop">
                    <div className="content clearfix">
                        <div className="full left align-center">
                            <span className="door"></span>
                            <h2>Login</h2>
                        </div>

                        <div className="sep-20"></div>

                        <Formik
                            initialValues={{
                                email: '',
                                password: '',
                                rememberMe: '',
                            }}
                            validationSchema={Schema}
                            onSubmit={values => {
                                // same shape as initial values
                                // console.log(values);
                                let data = {
                                    EmailAddress: values.email,
                                    Password: values.password,
                                    RememberMe: values.rememberMe
                                }
                                this.ApiLogin(data);
                            }}
                        >
                            {({ values, errors, touched, handleChange }) => (
                                <Form>
                                    <dl className="generalForm clearfix">
                                        <div>
                                            <dt></dt>
                                            <dd>
                                                <Link to={"/" + i18n.language + "/memberLogin"} className="button facebook">Login with Facebook</Link>
                                            </dd>
                                        </div>

                                        <div className="sep-25"></div>

                                        <div>
                                            <dt></dt>
                                            <dd>
                                                <Link to={"/" + i18n.language + "/memberLogin"} className="button googlePlus">Login with Google +</Link>
                                            </dd>
                                        </div>

                                        <div className="sep-25"></div>

                                        <div>
                                            <dt></dt>
                                            <dd className="align-center">
                                                OR
                                        </dd>
                                        </div>

                                        <div className="sep-25"></div>

                                        <div>
                                            <dt></dt>
                                            <dd>
                                                <Field name="email" type="email" />
                                                {errors.email && touched.email ? <ErrorMessage message={errors.email} /> : null}
                                            </dd>
                                        </div>

                                        <div className="sep-25"></div>

                                        <div>
                                            <dt></dt>
                                            <dd>
                                                <Field name="password" type="password" />
                                                {errors.password && touched.password ? <ErrorMessage message={errors.password} /> : null}
                                            </dd>
                                        </div>

                                        <div className="sep-10"></div>

                                        <div>
                                            <dt></dt>
                                            <dd className="align-right">
                                                <Link to={"/" + i18n.language + '/memberForgetPassword'}>Forgot password</Link>
                                            </dd>
                                        </div>

                                        <div className="sep-25"></div>

                                        <div>
                                            <dt></dt>
                                            <dd>
                                                <div className="checkboxFive">
                                                    <div>
                                                        <input type="checkbox" id="rememberMe" onChange={handleChange} checked={values.rememberMe} />
                                                        <label htmlFor="rememberMe"></label>
                                                    </div>
                                                    <label htmlFor="rememberMe">Remember me</label>
                                                </div>
                                                {errors.rememberMe && touched.rememberMe ? <ErrorMessage message={errors.rememberMe} /> : null}
                                            </dd>
                                        </div>


                                        <div className="sep-25"></div>

                                        <div>
                                            {this.state.errors &&
                                                <ErrorMessage message={this.state.errors} />
                                            }
                                        </div>
                                    </dl>

                                    <div className="sep-25"></div>
                                    <div className="sep-25"></div>


                                    <div className="full left align-center">
                                        <div>
                                            <button type="submit" className="button">Login</button>
                                            <div className="sep-10"></div>
                                            <Link to={"/" + i18n.language + "/memberCreateAccountStart"}>Don't have an account?</Link>
                                        </div>
                                    </div>
                                </Form>
                            )}
                        </Formik>
                    </div>
                </div >
            </div >
        );
    }
}

const mapStateToProps = (state) => (
    {
        members: state.members
    }
);

export default withTranslation()(connect(mapStateToProps)(MemberLogin));
