import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { withTranslation } from 'react-i18next';

import * as Config from '../../../config';
import FacebookLogin from 'react-facebook-login/dist/facebook-login-render-props';
import GoogleLogin from 'react-google-login';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as MembersActionCreators from '../../../actions/members';

class MemberCreateAccountStart extends Component {

	constructor(props) {

		super(props);

		const { dispatch } = props;

		this.ResetRegistration = bindActionCreators(MembersActionCreators.ResetRegistration, dispatch);

		this.ResetRegistration();

		this.responseFacebook = this.responseFacebook.bind(this);
		this.responseGoogle = this.responseGoogle.bind(this);
	}

	responseFacebook = (response) => {
		// console.log(response);
	}

	responseGoogle = (response) => {
		// console.log(response);
	}

	render() {
		const {
			// t,
			i18n
		} = this.props;

		return (
			<div className="wrapper-container memberLogin padding" >

				<div className="containerMain borderTop">
					<div className="content clearfix">
						<div className="full left align-center">
							<span className="door"></span>
							<h2>Let's get started</h2>
						</div>

						<div className="sep-20"></div>

						<dl className="generalForm clearfix">
							<div>
								<dt></dt>
								<dd>
									<FacebookLogin
										appId={Config.UAT_FACEBOOK_APP_ID}
										autoLoad={false}
										fields="name,email,picture"
										callback={this.props.responseFacebook}
										render={renderProps => (
											<button className="button facebook" onClick={renderProps.onClick}>Register with Facebook</button>
										)}
									/>
								</dd>
							</div>

							<div className="sep-25"></div>

							<div>
								<dt></dt>
								<dd>
									<GoogleLogin
										clientId={Config.UAT_GOOGLE_PLUS_clientId}
										render={renderProps => (
											<button className="button googlePlus" onClick={renderProps.onClick}>Register with Google +</button>
										)}
										buttonText="Register with Google +"
										onSuccess={this.props.responseGoogle}
										onFailure={this.props.responseGoogle}
									/>
								</dd>
							</div>

							<div className="sep-25"></div>

							<div>
								<dt></dt>
								<dd>
									<Link to={"/" + i18n.language + "/memberCreateAccount"}
										className="button registerWithEmail">Register with Email</Link>
								</dd>
							</div>

							<div className="sep-25"></div>

							<p>
								{/*TODO*/} By proceeding, you agree and accept the <Link to={"/" + i18n.language + "/terms"}>Terms and Conditions</Link>, Licence Agreement, <Link to={"/" + i18n.language + "/privacy"}>Privacy Policy</Link> and Disclaimer.
							</p>

							<div className="sep-25"></div>

							<p>
								Already have a Let's Park account? <Link to={"/" + i18n.language + "/memberLogin"}>Login here</Link>
							</p>

						</dl>
					</div>
				</div>
			</div >
		);
	}
}

const mapStateToProps = (state) => (
	{
		members: state.members
	}
);

export default withTranslation()(connect(mapStateToProps)(MemberCreateAccountStart));
