import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Redirect } from 'react-router';
import { withTranslation } from 'react-i18next';

import * as Config from '../../../config';
import FacebookLogin from 'react-facebook-login';
import GoogleLogin from 'react-google-login';

// import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
// import * as MembersActionCreators from "../../actions/members";

class MemberGetStarted extends Component {

    constructor(props) {

        super(props);

        // const { dispatch } = props;

        this.responseFacebook = this.responseFacebook.bind(this);
        this.responseGoogle = this.responseGoogle.bind(this);
    }

    responseFacebook = (response) => {
        //console.log(response);
    }

    responseGoogle = (response) => {
        // console.log(response);
    }

    render() {
        const {
            // t,
            i18n
        } = this.props;

        if (this.props.members.Login && this.props.members.Login.length !== 0) {
            return <Redirect push to='/' />;
        }

        return (
            <div className="wrapper-container memberLogin padding">

                <div className="containerMain borderTop">
                    <div className="content clearfix">
                        <div className="full left align-center">
                            <span className="door"></span>
                            <h2>Let's get started</h2>
                        </div>

                        <div className="sep-20"></div>

                        <dl className="generalForm clearfix">
                            <div>
                                <dt></dt>
                                <dd>
                                    <FacebookLogin
                                        appId={Config.UAT_FACEBOOK_APP_ID}
                                        autoLoad={false}
                                        fields="name,email,picture"
                                        callback={this.props.responseFacebook}
                                        cssClass="button facebook"
                                    />
                                </dd>
                            </div>

                            <div className="sep-25"></div>

                            <div>
                                <dt></dt>
                                <dd>
                                    <GoogleLogin
                                        clientId={Config.UAT_GOOGLE_PLUS_clientId}
                                        render={renderProps => (
                                            <button className="button googlePlus" onClick={renderProps.onClick}>Login with Google +</button>
                                        )}
                                        buttonText="Login with Google +"
                                        onSuccess={this.props.responseGoogle}
                                        onFailure={this.props.responseGoogle}
                                    />
                                </dd>
                            </div>

                            <div className="sep-25"></div>

                            <div>
                                <dt></dt>
                                <dd>
                                    <Link to={"/" + i18n.language + "/memberLogin"} className="button registerWithEmail">Login with Email</Link>
                                </dd>
                            </div>
                        </dl>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => (
    {
        members: state.members
    }
);

export default withTranslation()(connect(mapStateToProps)(MemberGetStarted));