import React, { Component } from 'react';

import CancellationZHHK from '../../components/10General/Cancellation_zh_HK';
import CancellationENUS from '../../components/10General/Cancellation_en_US';

class Disclaimer extends Component {

    render() {
        return (
            <div className="wrapper-container media">

                <div className="page-title"><h2>Cancellation</h2></div>

                <div className="sep-0"></div>

                <div className="containerMain">
                    <div className="content clearfix">
                        {this.props.i18n.language === 'zh-HK'
                            ? <CancellationZHHK />
                            : <CancellationENUS />
                        }
                    </div>
                </div>
            </div>
        );
    }
}

// const mapStateToProps = (state) => (
