import React, { Component } from 'react';
import { Link } from 'react-router-dom';

class ViewChangeButton extends Component {
    render() {
        return (
            <div className={"toggleViews " + (this.props.showView === 'map' ? 'absolute' : '')}>
                <ul className="clearfix">
                    {this.props.showView === 'list' &&
                        <li className="filter">
                            <Link to="/" className={(this.props.desktopFilter.showDesktopFilter ? 'active' : '')} onClick={(e) => this.props.handleDesktopFilter('toggleDesktopFilter', e)}>Filter</Link>
                        </li>
                    }

                    <li>
                        <Link to="/" className={(this.props.showView === 'map' ? 'active' : '')} onClick={(e) => this.props.handleViewChange(e, 'list')}>Map View</Link>
                    </li>
                    <li>
                        <Link to="/" className={(this.props.showView === 'list' ? 'active' : '')} onClick={(e) => this.props.handleViewChange(e, 'list')}>List View</Link>
                    </li>
                </ul>
            </div >
        )
    }
}

export default ViewChangeButton;
