import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import io from '../../socket/connect';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as MessagesActionCreators from '../../actions/messages';

import Landlord from '../../components/05Messages/Landlord';

class Messages extends Component {

    constructor(props) {

        super(props);

        const { dispatch } = props;

        // DISPATCH TO ACTIONS
        this.handleSocket = bindActionCreators(MessagesActionCreators.handleSocket, dispatch);

        this.pendingMessage = bindActionCreators(MessagesActionCreators.pendingMessage, dispatch);
        this.addMessage = bindActionCreators(MessagesActionCreators.addMessage, dispatch);
    }

    componentDidMount() {
        io.on('action', (data) => {
            this.addMessage(data);
        });
    }

    componentWillUnmount() {
        io.off('action')
    }

    render() {

        return (
            <div className="wrapper-container messages">
                <div className="containerMain">

                    <Landlord />

                    <div className="wrapper-formArea">
                        <div className="formArea clearfix">
                            <div className="fifty left">
                                <div>
                                    <h4>Booking type</h4>

                                    <div className="wrap clearfix">
                                        <div className="full">
                                            <div>
                                                <select>
                                                    <option> Hourly</option>
                                                    <option> Monthly</option>
                                                    <option> Timeshare</option>
                                                </select>
                                            </div>
                                        </div>
                                    </div>

                                    <div className="sep-20"></div>

                                    <h4>Suggested questions</h4>

                                    <div className="wrap clearfix">
                                        <div className="full">
                                            <div>
                                                <div className="checkboxFive green">
                                                    <div>
                                                        <input type="checkbox" id="notifyEmail" />
                                                        <label htmlFor="notifyEmail"></label>
                                                    </div>
                                                    <label htmlFor="notifyEmail">Is this a "wide load" parking space?</label>
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                    <div className="wrap clearfix">
                                        <div className="full">
                                            <div>
                                                <div className="checkboxFive green">
                                                    <div>
                                                        <input type="checkbox" id="sms" />
                                                        <label htmlFor="sms"></label>
                                                    </div>
                                                    <label htmlFor="sms">Is it suitable for novice driver?</label>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className="full left">
                                <div>
                                    <div className="wrap clearfix">
                                        <div className="full">
                                            <div>
                                                <textarea placeholder="Please type any additional questions here"></textarea>

                                                <div className="sep-20"></div>

                                                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Cras quis ante ac enim egestas vehicula rutrum in augue. Suspendisse nisl nisi, pellentesque ut accumsan sit amet </p>
                                            </div>
                                        </div>
                                    </div>

                                    <div className="wrap clearfix">
                                        <div className="full align-center">
                                            <div>
                                                <Link to={"/"} className="button">Send</Link>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => (
    {
        messages: state.messages
    }
);

export default connect(mapStateToProps)(Messages);
