import React, { Component } from 'react';
import { Redirect } from 'react-router';
import { withTranslation } from 'react-i18next';

import { Formik, Form, Field } from 'formik';
import * as Yup from 'yup';

import * as Config from '../../../config';
// import * as Helper from '../../utils/02Members/Validations';
import ErrorMessage from '../../../components/103Parts/ErrorMessage';

// import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
// import * as MembersActionCreators from '../../../actions/members';


// Form Validation
const Schema = Yup.object().shape({
    email: Yup.string()
        .email('Invalid email')
        .required('Email is Required'),
    password: Yup.string()
        .required('Password is Required')
        .matches(/^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,}$/, 'Password must be at least 6 characters including at least 1 digit and 1 letter'),
    confirmPassword: Yup.string()
        .oneOf([Yup.ref('password'), null], 'Passwords don\'t match'),
    firstName: Yup.string()
        .required('First Name is Required'),
    lastName: Yup.string()
        .required('Last Name is Required')
});

class MemberCreateAccount extends Component {
    constructor(props) {

        super(props);

        // const { dispatch } = props;

        // DISPATCH TO ACTIONS

        this.state = {
            email: '',
            formSubmitted: false,
            errors: []
        }

        this.ApiRegister = this.ApiRegister.bind(this);
    }

    ApiRegister = (data) => {

        var val = '9' + Math.floor(1000000 + Math.random() * 900000);

        var body = {
            name: data.FirstName,
            email: data.EmailAddress,
            passwd: data.Password,
            phone: val,
            isCorp: false,
            marketing: data.directMarketing
        }

        fetch(Config.API_URL + 'users', {
            method: 'post',
            headers: new Headers({
                //'Authorization': 'Basic ' + btoa(Config.UAT_API_LOGIN + ':' + Config.UAT_API_PASSWORD),
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }),
            body: JSON.stringify(body)
        }).then(r => r.json().then(data => ({ status: r.status, body: data })
        )).then(obj => {
            console.log(obj);
            if (obj.status === 200) {

                // const postData = {
                //     userToken: obj.body.token
                // }

                this.setState({
                    ...this.state,
                    formSubmitted: true,
                    email: data.EmailAddress
                })

            } else if (obj.status === 400) {

                this.setState({
                    ...this.state,
                    errors: obj.body.msg
                });
            }

        }).catch(error => {
            console.log(error)
        });
    }

    render() {
        const {
            // t,
            i18n
        } = this.props;

        console.log(this.state.errors)

        if (this.state.formSubmitted === true) {
            return <Redirect to={"/" + i18n.language + "/memberCreateAccountComplete?email=" + this.state.email} />;
        }
        return (
            <div className="wrapper-container memberCreateAccount padding">
                <div className="containerMain borderTop">
                    <div className="content clearfix">
                        <div className="full left align-center">
                            {/*<span className="door"></span>*/}
                            <h2>Welcome</h2>
                            <p>Create an account.</p>
                        </div>

                        <div className="sep-20"></div>

                        <Formik
                            initialValues={{
                                email: '',
                                password: '',
                                confirmPassword: '',
                                firstName: '',
                                lastName: '',
                                directMarketing: false,
                            }}
                            validationSchema={Schema}
                            onSubmit={values => {
                                // same shape as initial values
                                // console.log(values);
                                let data = {
                                    EmailAddress: values.email,
                                    Password: values.password,
                                    ConfirmPassword: values.confirmPassword,
                                    FirstName: values.firstName,
                                    LastName: values.lastName,
                                    directMarketing: values.directMarketing,
                                }
                                this.ApiRegister(data);
                            }}
                        >
                            {({ values, errors, touched, handleChange }) => (
                                <Form>
                                    <dl className="generalForm clearfix">
                                        <div>
                                            <dt><h4>Email Address</h4></dt>
                                            <dd>
                                                <Field name="email" type="email" />
                                                {errors.email && touched.email ? <ErrorMessage message={errors.email} /> : null}
                                            </dd>
                                        </div>

                                        <div className="sep-25"></div>

                                        <div>
                                            <dt><h4>Password</h4></dt>
                                            <dd>
                                                <Field name="password" type="password" />
                                                {errors.password && touched.password ? <ErrorMessage message={errors.password} /> : null}
                                            </dd>
                                        </div>

                                        <div className="sep-25"></div>

                                        <div>
                                            <dt><h4>Confirm Password</h4></dt>
                                            <dd>
                                                <Field name="confirmPassword" type="password" />
                                                {errors.confirmPassword && touched.confirmPassword ? <ErrorMessage message={errors.confirmPassword} /> : null}
                                            </dd>
                                        </div>

                                        <div className="sep-25"></div>

                                        <div>
                                            <dt><h4>First Name</h4></dt>
                                            <dd>
                                                <Field name="firstName" type="text" />
                                                {errors.firstName && touched.firstName ? <ErrorMessage message={errors.firstName} /> : null}
                                            </dd>
                                        </div>

                                        <div className="sep-25"></div>

                                        <div>
                                            <dt><h4>Last Name</h4></dt>
                                            <dd>
                                                <Field name="lastName" type="text" />
                                                {errors.lastName && touched.lastName ? <ErrorMessage message={errors.lastName} /> : null}
                                            </dd>
                                        </div>

                                        <div className="sep-25"></div>

                                        <div>
                                            <dt></dt>
                                            <dd>
                                                <div className="checkboxFive">
                                                    <div>
                                                        <input type="checkbox" id="directMarketing" name="directMarketing" onChange={handleChange} />
                                                        <label htmlFor="directMarketing"></label>
                                                    </div>
                                                    <label htmlFor="directMarketing">I object to the propsoal at any person data in direct marketing</label>
                                                </div>

                                                {this.state.errors &&
                                                    <div className="sep-20"></div>
                                                }

                                                {this.state.errors &&
                                                    <ErrorMessage
                                                        message={this.state.errors}


                                                    />
                                                }
                                            </dd>
                                        </div>
                                    </dl>

                                    <div className="sep-25"></div>
                                    <div className="sep-25"></div>

                                    <div className="full left align-center">
                                        <div>
                                            <button type="submit" className="button">Submit</button>
                                        </div>
                                    </div>
                                </Form>
                            )}
                        </Formik>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => (
    {
        members: state.members
    }
);

export default withTranslation()(connect(mapStateToProps)(MemberCreateAccount));
