import React, { Component } from 'react';
import { withTranslation } from 'react-i18next';
import { Tabs, Tab } from 'react-bootstrap';

import DatePicker from "react-datepicker";
import moment from "moment";

import ViewChangeWithSlider from "../../components/03FindParkingSpace/ViewChangeWithSlider";
import PrevNext from '../../components/04ListASpace/PrevNext';
import Reviews from '../../components/103Parts/Reviews';
import Api from '../../api/Api'
import SocialShare from '../../components/103Parts/SocialShare';

// import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
// import * as listParkingSpaceActionCreators from '../../actions/listParkingSpace';

class DetailShare extends Component {

    constructor(props) {

        super(props);

        // const { dispatch } = props; 

        // DISPATCH TO ACTIONS
        //this.toggleAvailableDays = bindActionCreators(listParkingSpaceActionCreators.toggleAvailableDays, dispatch);

        this.state = {
            view: 'photo',
            wishlist: {
                0: true
            }
        };

        this.handleChangeView = this.handleChangeView.bind(this);
    }

    handleChangeView = (e, data1, data2) => {
        e.preventDefault();
        this.setState({
            ...this.state,
            [data1]: data2
        })
    }

    render() {
        const {
            // t,
            i18n
        } = this.props;

        const { listParkingSpace,
            //translate 
        } = this.props;

        return (
            <div className="wrapper-container detailShare">
                <div className="containerMain">
                    <div className="content clearfix">
                        <div className="information left">
                            <div className="text">
                                <h3>Causeway Bay, COFCO Tower</h3>
                                <h4>262 Cloucester Road, Causeway Bay</h4>
                                <div className="sep-0"></div>
                                <Reviews
                                    score={4} reviews={100}


                                />

                                <div className="sep-25"></div>

                                <ul className="tags clearfix">
                                    <li>Near entrance</li>
                                    <li>Near elevator/lift</li>
                                    <li>Near stairs</li>
                                    <li>Near block D</li>
                                    <li>Cornered Space</li>
                                </ul>

                                <div className="sep-20"></div>
                            </div>

                            <ViewChangeWithSlider
                                view={this.state.view}
                                handleChangeView={this.handleChangeView}
                                wishlist={this.state.wishlist}


                            />

                            <div className="sep-40"></div>

                            <div className="landlord clearfix">

                                <div className="fifty clearfix">

                                    <div className="image">
                                        <img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" />
                                    </div>
                                    <div className="text">
                                        <h3>Max Cheung</h3>
                                        <div className="memberSince">Member Since <span>Mar 29, 2017</span></div>
                                        <div className="sep-10"></div>
                                        <a href="/" className="button">Contact Owner</a>
                                    </div>

                                </div>

                                <div className="fifty clearfix">

                                    <div className="response">
                                        <div className="rate">Avg response rate <span>80%</span></div>
                                        <div className="time">Avg response time <span>12 mins</span></div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div className="book left">
                            <select>
                                <option>Time Share</option>
                                <option>Time Share</option>
                                <option>Time Share</option>
                                <option>Time Share</option>
                            </select>

                            <div className="sep-15"></div>

                            <div className="available-days">Available day(s) and times
                                <span className="check"
                                    onClick={(e) => this.toggleAvailableDays()}
                                >&nbsp;</span>
                                {listParkingSpace.showAvailableDays ? <Api /> : ''}
                            </div>

                            <div className="sep-20"></div>

                            <div className="radioFive">
                                <div>
                                    <input type="radio" id="non-reserve" name="reserve" value="1900"
                                        checked={listParkingSpace.price === 1900}
                                        onChange={(e) => this.handlePriceChange(1900)}
                                    />
                                    <label htmlFor="non-reserve"></label>
                                </div>
                                <label htmlFor="non-reserve">Non-reserved : HKD <span>1,900</span> per month</label>
                            </div>

                            <div className="sep-10"></div>

                            <div className="radioFive">
                                <div>
                                    <input type="radio" id="reserve" name="reserve" value="2300"
                                        checked={listParkingSpace.price === 2300}
                                        onChange={(e) => this.handlePriceChange(2300)}
                                    />
                                    <label htmlFor="reserve"></label>
                                </div>
                                <label htmlFor="reserve">Reserved : HKD <span>2,300</span> per month</label>
                            </div>

                            {/* <input type="radio" />  */}
                            {/* <div className="sep-10"></div> */}
                            {/* <input type="radio" /> Reserved : HKD <span>2,300</span> per month */}

                            <div className="sep-20"></div>

                            <DatePicker
                                dateFormat="YYYY-MM-DD"
                                selected={listParkingSpace.startDate === "" ? null : listParkingSpace.startDate}
                                onChange={(e) => this.handleStartDateChange(e)}
                                excludeDates={[
                                    moment().add(3, "days"),
                                    moment().add(5, "days"),
                                    moment().add(7, "days"),
                                    moment().add(9, "days"),
                                    moment().add(11, "days"),
                                    moment().add(13, "days"),
                                    moment().add(15, "days")
                                ]}
                                minDate={moment()}
                                placeholderText="Start Date"
                            />

                            <div className="sep-15"></div>

                            <DatePicker
                                dateFormat="YYYY-MM-DD"
                                selected={listParkingSpace.endDate === "" ? null : listParkingSpace.endDate}
                                onChange={(e) => this.handleEndDateChange(e)}
                                minDate={moment()}
                                // maxDate={moment().add(5, "days")}
                                placeholderText="End Date"
                            />

                            <div className="sep-20"></div>

                            <div className="unavailable-days">Unavailable day(s)
                                <span className="check"
                                    onClick={(e) => this.toggleUnavailableDays()}
                                >&nbsp;</span>
                                {listParkingSpace.showUnavailableDays ? <Api /> : ''}
                            </div>

                            <div className="sep-20"></div>

                            <dl className="clearfix">
                                <dt>01/01/2018 - 31/01/2018</dt>
                                <dd>HKD 1,900</dd>
                                <div className="sep-0"></div>
                                <dt>Deposit</dt>
                                <dd>HKD 1,900</dd>
                                <div className="sep-0"></div>
                                <dt><b>Total Amount</b></dt>
                                <dd><b>HKD 3,800</b></dd>
                            </dl>

                            <div className="sep-10"></div>

                            <p className="remarks">20% off for one-off 12 month payment<br />
                                Minimum 3 months booking<br />
                                Full calendar month booking (except the first month)</p>

                            <div className="sep-20"></div>

                            <a href="/" className="button bookNow">Book Now</a>

                            <div className="sep-15"></div>

                            <p className="notChargedYet">You won't be charged yet</p>

                            <div className="sep-15"></div>

                            <hr />

                            <div className="sep-15"></div>

                            <table className="verifiedGuarantee">
                                <tbody>
                                    <tr>
                                        <td> <img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" /></td>
                                        <td>Land search <span>Verified</span></td>
                                    </tr>
                                    <tr>
                                        <td> <img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" /></td>
                                        <td>You are always protected by our Parker Protection <span>Guarantee</span></td>
                                    </tr>
                                </tbody>
                            </table>

                            <div className="sep-15"></div>

                            <SocialShare


                            />
                        </div>

                        <div className="sep-30"></div>

                        <div className="wrapper-tabs">
                            <Tabs defaultActiveKey={1} id="uncontrolled-tab-example">
                                <Tab eventKey={1} title="Details">
                                    <div className="inner">
                                        <h5>Space suitable for</h5>

                                        <ul className="attributes">
                                            <li>Private Car</li>
                                            <li>Lorry (5.5 tones)</li>
                                        </ul>

                                        <div className="sep-25"></div>

                                        <h5>Feature</h5>

                                        <ul className="attributes">
                                            <li>With covers</li>
                                            <li>EV</li>
                                            <li>Headroom 2.2m</li>
                                        </ul>

                                        <div className="sep-25"></div>

                                        <h5>Restrictions</h5>

                                        <p>For landlord and parkers in this building</p>
                                    </div>
                                </Tab>
                                <Tab eventKey={2} title="Reviews">
                                    <div className="inner">
                                        Tab 2 content
                                    </div>
                                </Tab>
                            </Tabs>
                        </div>
                        <PrevNext
                            prevHas={true}
                            prevURL={"/" + i18n.language + "/stepFour"}
                            prevText='Back'
                            nextURL={"/" + i18n.language + "/detailShare"}
                            nextText='List'
                            progress="three"
                            value="3/3"
                        />
                    </div>
                </div>
            </div >
        );
    }
}

const mapStateToProps = state => (
    {
        listParkingSpace: state.listParkingSpace
    }
);

export default withTranslation()(connect(mapStateToProps)(DetailShare));
