import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { withTranslation } from 'react-i18next';

import moment from "moment";
import io from '../../socket/connect';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as MessagesActionCreators from '../../actions/messages';

class Landlord extends Component {
	render() {
		return (
			<div className="spaceInfo clearfix">
				<div className="person right">
					<div className="image">
						<img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" />
					</div>

					<h3>Max Cheung</h3>

					<div className="memberSince">Member Since <span>Mar 29, 2017</span></div>
				</div>
				<div className="space left">
					<h3>Causeway Bay, COFCO Tower</h3>
					<h4>262 Cloucester Road, Causeway Bay</h4>
					<span className="spaceId">Space ID - S005682</span>
				</div>
			</div>
		)
	}
}

class Response extends Component {
	render() {
		return (
			<div className="response">
				<div className="info clearfix">
					<h5>23/12 Fri 08:55 Booking request - Accepted</h5>

					<div className="image">
						<img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" />
					</div>
					<div className="text">
						<h3>Causeway Bay, COFCO Tower</h3>
					</div>

					<div className="sep-30"></div>

					<dl className="clearfix">
						<dt>
							<h4>Booking number</h4>
							<h5>B004245</h5>
						</dt>
						<dd>
							<h4>Total</h4>
							<h5>HKD 29,300</h5>
						</dd>
						<div className="sep-10"></div>
						<dt>
							<h4>Booking period</h4>
							<h5>01/01/2018 - 31/12/2018</h5>
						</dt>
						<dd>
							<h4>Service fee</h4>
							<h5>HKD 879</h5>
						</dd>
						<div className="sep-10"></div>
						<dt>
							<h4>Fee type</h4>
							<h5>Monthly</h5>
						</dt>
						<dd>
							<h4>Total received</h4>
							<h5>HKD 28,421</h5>
						</dd>
					</dl>
					<div className="sep-10"></div>

					<div className="align-center">
						<Link to="/" className="button">Looks great, Let's Park</Link>
						<div className="sep-10"></div>
						<Link to="/" className="button grey">Cancel booking</Link>
					</div>
				</div>
			</div>
		)
	}
}

class Block extends Component {
	render() {
		if (this.props.type === 'self') {
			return (
				<div className="wrapper-message mine" id={this.props.id}>
					<div className="message">
						<span className="dateTime">{moment(this.props.time).format('MM/DD ddd HH:mm')}</span>
						<div className="sep-0"></div>
						<div className="content">{this.props.content}</div>
					</div>
				</div>
			)
		} else {
			return (
				<div className="wrapper-message him" id={this.props.id}>
					<div className="message">
						<span className="dateTime">{moment(this.props.time).format('MM/DD ddd HH:mm')}</span>
						<div className="sep-0"></div>
						<div className="content">{this.props.content}</div>
					</div>
				</div>
			)
		}
	}
}

class Cluster extends Component {
	render() {
		// console.log(props);
		return (
			<div>
				{this.props.messages.map((message, index) =>
					<Block
						key={index}
						keyshow={index}
						id={message.id}
						type={message.type}
						time={message.time}
						content={message.content}
					/>
				)}
			</div>
		)
	}
}

class Results extends Component {

	constructor(props) {

		super(props);

		const { dispatch } = props;

		// DISPATCH TO ACTIONS
		this.handleSocket = bindActionCreators(MessagesActionCreators.handleSocket, dispatch);

		this.pendingMessage = bindActionCreators(MessagesActionCreators.pendingMessage, dispatch);
		this.addMessage = bindActionCreators(MessagesActionCreators.addMessage, dispatch);
	}

	componentDidMount() {
		io.on('action', (data) => {
			this.addMessage(data);
		});
	}

	componentWillUnmount() {
		io.off('action')
	}

	render() {
		// const { t, i18n } = this.props;

		const { messages,
			//translate 
		} = this.props;

		return (
			<div className="wrapper-container messages">
				<div className="containerMain">

					<Landlord />

					<Response />

					<div className="dialogues clearfix">

						<Cluster
							messages={messages.messages}
						/>

						{/* <div className="wrapper-message mine">
                            <div className="message">
                                <span className="dateTime">22/12 Fri 08:55</span>
                                <div className="content">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Pellentesque metus lectus, posuere ac nisi lobortis, rhoncus porttitor neque. Nunc molestie, mi ut convallis ornare, tortor arcu aliquam libero, a fringilla eros ipsum quis tortor. Sed id felis nec magna accumsan laoreet non ac ligula. Ut rhoncus dignissim ipsum, a tincidunt nulla feugiat vel. Cras egestas ante in turpis dictum faucibus. Aliquam vitae mi at tellus lacinia commodo eget a metus.</div>
                            </div>
                        </div>
                        <div className="wrapper-message him">
                            <div className="message">
                                <span className="dateTime">22/12 Fri 08:55</span>
                                <div className="content">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Pellentesque metus lectus, posuere ac nisi lobortis, rhoncus porttitor neque. Nunc molestie, mi ut convallis ornare, tortor arcu aliquam libero, a fringilla eros ipsum quis tortor. Sed id felis nec magna accumsan laoreet non ac ligula. Ut rhoncus dignissim ipsum, a tincidunt nulla feugiat vel. Cras egestas ante in turpis dictum faucibus. Aliquam vitae mi at tellus lacinia commodo eget a metus.</div>
                            </div>
                        </div>
                        <div className="wrapper-message mine">
                            <div className="message">
                                <span className="dateTime">22/12 Fri 08:55</span>
                                <div className="content">Lorem ipsum dolor sit amet, consectetur adipiscing elit. Pellentesque metus lectus, posuere ac nisi lobortis, rhoncus porttitor neque. Nunc molestie, mi ut convallis ornare, tortor arcu aliquam libero, a fringilla eros ipsum quis tortor. Sed id felis nec magna accumsan laoreet non ac ligula. Ut rhoncus dignissim ipsum, a tincidunt nulla feugiat vel. Cras egestas ante in turpis dictum faucibus. Aliquam vitae mi at tellus lacinia commodo eget a metus.</div>
                            </div>
                        </div> */}

						<div className="wrapper-messageEntry">
							<textarea className="messageEntry"
								onChange={(e) => this.pendingMessage(e.target.value)}
								value={messages.pendingMessage}
								placeholder="Type a message..."></textarea>

							<div className="messageSend"
								onClick={(e) => this.handleSocket(messages.pendingMessage)}
							></div>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

const mapStateToProps = (state) => (
	{
		messages: state.messages
	}
);

export default withTranslation()(connect(mapStateToProps)(Results));
