import React, { Component } from 'react';
// import { Redirect } from 'react-router';
import { Link } from 'react-router-dom';

import DatePicker from "react-datepicker";
import moment from "moment";
import TimePicker from "../../components/103Parts/TimePicker";
import * as Helper from "../../utils/03FindParkingSpace/Calculations";
import { lowestPrice, isSamePrice} from "../../utils/03FindParkingSpace/LowestHourlyPrice";

// import { bindActionCreators } from 'redux';
// import { connect } from 'react-redux';
// import * as FindParkingSpaceActionCreators from '../../actions/findParkingSpace';

class Payment extends Component {
	render() {
		return (
			<div>
				<Link to="/" className={this.props.className + " button bookNow"} onClick={(e) => {e.preventDefault(); this.props.goToPayment(e)}}>Looks Great, Let's Go</Link>

				<div className="sep-15"></div>

				<p className="notChargedYet">You won't be charged yet</p>
			</div>
		)
	}
}

class Total extends Component {
	render() {
		return (
			<dl className="clearfix">
				{/*<dt>HKD 20 x {this.props.calculation.hours} hour(s)</dt>*/}
				{/*<dd>HKD {Helper.number_format(this.props.calculation.amount)}</dd>*/}
				{/*<div className="sep-0"></div>*/}
				<dt><b>Total Amount</b></dt>
				<dd><b>HKD  {Helper.number_format(this.props.calculation.total, 0)}</b></dd>
			</dl>
		)
	}
}

class Price extends Component {
	constructor(props) {
		super(props);

		this.state = {
			open: false
		};

		this.handleBtnClick = this.handleBtnClick.bind(this);
	}

	handleBtnClick = () => {
		const open = !this.state.open;
		this.setState({
			...this.state,
			open: open
		})
	}

	render() {
		let price = lowestPrice(this.props.days),
			samePrice = isSamePrice(this.props.days);

		return (
			<div className="price">

				{/*{this.props.hourlyData.instant &&*/}
				{/*<div className="instantBooking"></div>*/}
				{/*}*/}

				<div className="unavailable-days">
					{!samePrice ? "From" : ""} HKD <span>{price}</span> per hour

					{!samePrice &&
					<span className="check" onClick={this.handleBtnClick}>&nbsp;</span>
					}

					{!samePrice && this.state.open &&
					<div className="popup">
						<dl>
							<div>
								{ this.props.days.map((t, index) => {
									return (
										<div key={index}>
											<dt>{t("Days.day_" + t.day)}</dt>
											<dd>HKD {t.price}</dd>
											<div className="sep-0"></div>
										</div>
									)
								})}
							</div>
						</dl>
					</div>
					}
				</div>
			</div>
		);
	}
}

class DetailHourly extends Component {

	onDatepickerRef = (el) => {
		if (el && el.input) {
			el.input.readOnly = true;
		}
	}

	render() {

		// const { 
		// 	findParkingSpace,
		//     translate
		// } = this.props;

		// if (this.state.formSubmit) {
		// 	return <Redirect to={"/" + i18n.language + "/payment"} />;
		// }



		const booking_types = {
			'hourly': 'Hourly',
			'monthly': 'Monthly',
			'timeshare': 'Timeshare',
		}

		const supportTypeSelect = this.props.supportTypes.map((t, index) =>
			<option value={t} key={index}>{booking_types[t]}</option>
		);

		let availability = null;
		if (typeof (this.props.calculation.success) === "boolean") {
			if (this.props.calculation.success)
				availability = <Total calculation={this.props.calculation} />
			else
				availability = <div>Sorry, time not available.</div>
		}

		return (
			<div className="book left">
				<select value={this.props.BookingType}
					onChange={(e) => this.props.handleBookingType(e.target.value)}>
					{supportTypeSelect}
				</select>

				<div className="sep-20"></div>

				<Price days={this.props.hourlyData.days} />

				<div className="sep-30"></div>

				<h3>From</h3>
				<div className="wrap clearfix">
					<div className="fifty">
						<div>
							<DatePicker
								dateFormat="YYYY-MM-DD"
								selected={this.props.startDate === "" ? null : this.props.startDate}
								onChange={(e) => this.props.updateState('toBookHourly', 'startDate', e)}
								minDate={moment()}
								maxDate={!this.props.endDate || this.props.endDate === "" ? null : moment(this.props.endDate)}
								placeholderText="Start Date"
								ref={el => this.onDatepickerRef(el)}
							/>
						</div>
					</div>
					<div className="fifty">
						<div>
							<TimePicker onChangeHandler={(e) => this.props.updateState('toBookHourly', 'startTime', e)} />
						</div>
					</div>
				</div>

				<h3>To</h3>
				<div className="wrap clearfix">
					<div className="fifty">
						<div>
							<DatePicker
								dateFormat="YYYY-MM-DD"
								selected={this.props.endDate === "" ? null : this.props.endDate}
								onChange={(e) => this.props.updateState('toBookHourly', 'endDate', e)}
								minDate={!this.props.startDate || this.props.startDate === "" ? moment() : moment(this.props.startDate)}
								// maxDate={moment().add(5, "days")}
								placeholderText="End Date"
								ref={el => this.onDatepickerRef(el)}
							/>
						</div>
					</div>
					<div className="fifty">
						<div>
							<TimePicker onChangeHandler={(e) => this.props.updateState('toBookHourly', 'endTime', e)} />
						</div>
					</div>
				</div>

				{availability}

				<div className="sep-20"></div>

				<table className="verifiedGuarantee">
					<tbody>
						<tr>
							<td> <img src={require('../../images/mobile/03_find_parking_space/icon_verified.png')} alt="" /></td>
							<td>Land search <span>Verified</span></td>
						</tr>
						<tr>
							<td> <img src={require('../../images/mobile/03_find_parking_space/icon_guarantee.png')} alt="" /></td>
							<td>You are always protected by our Parker Protection <span>Guarantee</span></td>
						</tr>
					</tbody>
				</table>

				<div className="sep-20"></div>

				<Payment
					className={!this.props.calculation.success ? "grey" : ""}
					calculation={this.props.calculation}
					goToPayment={this.props.calculation.success ? this.props.goToPayment : ()=>{}}
				/>
			</div>
		);
	}
}

