import React, { Component } from 'react';
import { withTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import { Tabs, Tab } from 'react-bootstrap';

import ListType1 from '../../components/102Grids/ListType1';
import Reviews from '../../components/103Parts/Reviews';

// import { bindActionCreators } from 'redux';
// import { connect } from 'react-redux';
// import * as FindParkingSpaceActionCreators from '../../actions/findParkingSpace';

class LandlordProfile extends Component {

    constructor(props) {
        super(props);

        this.state = {
            carTypes: ['Indoor', 'NoCovers'],
            verified: {
                2: true
            },
            wishlist: {
                1: true
            }
        }
    }

    handleVerified = (id) => {
        this.setState({
            verified: {
                ...this.state.verified,
                [id]: !this.state.verified[id]
            }
        });
    }

    handleWishlist = (id) => {
        this.setState({
            wishlist: {
                ...this.state.wishlist,
                [id]: !this.state.wishlist[id]
            }
        });
    }

    render() {

        // const { translate } = this.props;

        return (
            <div className="wrapper-container landlordProfile">

                {/* <div className="page-title"><h2>Contact Us</h2></div> */}

                <div className="sep-0"></div>

                <div className="containerMain">

                    <div className="content clearfix">

                        <div className="landlord left">
                            <div className="image">
                                <img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" />
                            </div>

                            <h3>Max Cheung</h3>

                            <div className="memberSince">Member Since <span>Mar 29, 2017</span></div>

                            <div className="Verified">Email and Mobile number verified</div>

                            <div className="response">
                                <div className="rate">Avg response rate <span>80%</span></div>
                                <div className="time">Avg response time <span>12 mins</span></div>
                            </div>
                        </div>

                        <div className="profile left">
                            <div className="wrapper-tabs">
                                <Tabs defaultActiveKey={1} id="uncontrolled-tab-example">
                                    <Tab eventKey={1} title="Current listing">
                                        <div className="tab-content">

                                            <ListType1
                                                verified={this.state.verified}
                                                handleVerified={this.handleVerified}
                                                wishlist={this.state.wishlist}
                                                handleWishlist={this.handleWishlist}
                                                carTypes={this.state.carTypes}
                                            />

                                            <div className="sep-20"></div>

                                            <div className="dialog clearfix">
                                                <div className="image">
                                                    <img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" />
                                                </div>
                                                <div className="text">
                                                    <span className="username">Username</span> <span className="date">06/10/2017</span> <Link to="/" className="reply">Reply</Link>

                                                    <div className="sep-10"></div>

                                                    <Reviews score={4} reviews={100} />

                                                    <div className="sep-10"></div>

                                                    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat.</p>

                                                    <div className="sub-dialog">
                                                        <div className="image">
                                                            <img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" />
                                                        </div>
                                                        <div className="text">
                                                            <span className="username">Username</span> <span className="date">06/10/2017</span>

                                                            <div className="sep-10"></div>

                                                            <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</p>
                                                        </div>
                                                    </div>
                                                </div>

                                                <div className="sep-0"></div>

                                                <Link to="/" className="moreComments">More Comments</Link>
                                            </div>

                                            <div className="sep-20"></div>
                                        </div>
                                    </Tab>
                                    <Tab eventKey={2} title="Past booking">
                                        No past booking
                                    </Tab>
                                </Tabs>
                            </div>


                            {/* <ul className="tab-head clearfix">
                                <li><Link to="/" className="active">Current listing</Link></li>
                                <li><Link to="/">Past booking</Link></li>
                            </ul>

                            <div className="sep-30"></div>

                             */}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

// const mapStateToProps = (state) => (
// 	{
// 	}
// );

export default withTranslation()(LandlordProfile);
// connect(mapStateToProps)(Contact);
