import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import Heart from '../103Parts/Heart';
import Slider from "react-slick";
import SpaceGoogleMap from "../../components/103Parts/SpaceGoogleMap";
import GoogleMapStreetView from "../../components/103Parts/GoogleMapStreetView";

class ViewChangeWithSlider extends Component {

    constructor(props) {
        super(props);

        // react slick settings
        this.settings = {
            customPaging: function (i) {
                return (
                    <div>
                        <a href="/" onClick={(e) => e.preventDefault()} >
                            <img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" />
                        </a>
                    </div>
                );
            },
            dots: true,
            dotsClass: "slick-dots",
            infinite: true,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1
        };
    }

    render() {
        return (
            <div className={"super-wrapper " + (this.props.view === 'photo' ? 'active' : '')}>
                <img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" />
                <Heart
                    wishlist={this.props.wishlist}
                    handleWishlist={this.props.handleWishlist}
                    
                    
                />
                <ul className="views clearfix">
                    <li><Link to="/" className={"mapView " + (this.props.view === 'map' ? 'active' : '')}
                        onClick={(e) => this.props.handleChangeView(e, 'view', 'map')}>Map View</Link></li>
                    <li><Link to="/" className={"streetView " + (this.props.view === 'street' ? 'active' : '')}
                        onClick={(e) => this.props.handleChangeView(e, 'view', 'street')}>Street View</Link></li>
                    <li><Link to="/" className={"photoView " + (this.props.view === 'photo' ? 'active' : '')}
                        onClick={(e) => this.props.handleChangeView(e, 'view', 'photo')}>Photo View</Link></li>
                </ul>

                {
                    (this.props.view === 'map') ? <div className="clusterMap"><SpaceGoogleMap lat={this.props.lat} lng={this.props.lng} /></div>
                        : (this.props.view === 'street') ? <div className="clusterMap"><GoogleMapStreetView lat={this.props.lat} lng={this.props.lng} /></div>
                            : <Slider {...this.settings}>
                                <div>
                                    <img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" />
                                </div>
                                <div>
                                    <img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" />
                                </div>
                                <div>
                                    <img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" />
                                </div>
                                <div>
                                    <img src={require('../../images/mobile/General/dummy-img/img_milestone2.jpg')} alt="" />
                                </div>
                            </Slider>
                }
            </div>
        )
    }
}

export default ViewChangeWithSlider;
