import React, { Component } from 'react';
import { Tabs, Tab } from 'react-bootstrap';
import { withTranslation } from 'react-i18next';

import ListType3 from '../../../components/102Grids/ListType3';
import MemberMenuDesktop from '../../../components/02Members/MemberMenuDesktop';
import MemberMenuMobile from '../../../components/02Members/MemberMenuMobile';

// import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
// import * as MembersActionCreators from '../../actions/members';

function Block(props) {
    return (
        <ListType3
            id={props.id}
            verified={props.verified}
            handleVerified={props.handleVerified}
            wishlist={props.wishlist}
            handleWishlist={props.handleWishlist}
            carTypes={props.carTypes}
            score={props.score}
            reviews={props.reviews}
        />
    )
}

function Cluster(props) {
    let rows = [];
    for (let i = 0; i < 6; i++) {
        rows.push(
            <div className="full left" key={i}>
                <Block
                    key={i}
                    id={i}
                    verified={props.verified}
                    handleVerified={props.handleVerified}
                    wishlist={props.wishlist}
                    handleWishlist={props.handleWishlist}
                    carTypes={props.carTypes}
                    score={props.score}
                    reviews={props.reviews}
                />
            </div>
        )
    }
    return (rows);
}

class MemberListedSpaces extends Component {
    constructor(props) {
        super(props);

        this.state = {
            score: 2,
            reviews: 130,
            carTypes: ['Indoor', 'EV'],
            verified: {
                2: true
            },
            wishlist: {
                1: true
            }
        }
    }

    handleVerified = (id) => {
        this.setState({
            verified: {
                ...this.state.verified,
                [id]: !this.state.verified[id]
            }
        });
    }

    handleWishlist = (id) => {
        this.setState({
            wishlist: {
                ...this.state.wishlist,
                [id]: !this.state.wishlist[id]
            }
        });
    }

    render() {
        // const {t} = this.props;
        return (
            <div className="wrapper-container memberWishList">
                <MemberMenuMobile />
                <div className="containerMain paddingFix">
                    <MemberMenuDesktop />

                    <div className="content clearfix">
                        <div className="wrapper-tabs clearfix">
                            <Tabs defaultActiveKey={1} id="uncontrolled-tab-example">
                                <Tab eventKey={1} title="Current Listings">
                                    <Cluster
                                        verified={this.state.verified}
                                        handleVerified={this.handleVerified}
                                        wishlist={this.state.wishlist}
                                        handleWishlist={this.handleWishlist}
                                        carTypes={this.state.carTypes}
                                        score={this.state.score}
                                        reviews={this.state.reviews}
                                    />
                                </Tab>
                                <Tab eventKey={2} title="Current Bookings">
                                    <Cluster
                                        verified={this.state.verified}
                                        handleVerified={this.handleVerified}
                                        wishlist={this.state.wishlist}
                                        handleWishlist={this.handleWishlist}
                                        carTypes={this.state.carTypes}
                                        score={this.state.score}
                                        reviews={this.state.reviews}
                                    />
                                </Tab>
                                <Tab eventKey={3} title="Past Bookings">
                                    <Cluster
                                        verified={this.state.verified}
                                        handleVerified={this.handleVerified}
                                        wishlist={this.state.wishlist}
                                        handleWishlist={this.handleWishlist}
                                        carTypes={this.state.carTypes}
                                        score={this.state.score}
                                        reviews={this.state.reviews}
                                    />
                                </Tab>
                            </Tabs>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = (state) => (
    {
        members: state.members
    }
);

export default withTranslation()(connect(mapStateToProps)(MemberListedSpaces));
