import React, { Component } from 'react';
import { withTranslation } from 'react-i18next';


// import { bindActionCreators } from 'redux';
// import { connect } from 'react-redux';
// import * as InviteeActionCreators from '../actions/invitee';

import ErrorMessage from '../../components/103Parts/ErrorMessage';

class Contact extends Component {

    constructor(props) {

        super(props)

        // const { dispatch } = props;

        // DISPATCH TO ACTIONS
        // this.Contact = bindActionCreators(ContactActionCreators.Contact, dispatch);

        this.state = {
            formSubmitted: false,
            name: {
                title: 'mr',
                lastName: '',
            },
            email: '',
            mobile: '',
            enquiryType: '',
            message: '',
            tnc: false,
            Errors: {},
        };
    }

    changeStateLevelOne = (data1, value) => {
        this.setState({
            [data1]: value
        })
    }

    changeStateLevelTwo = (data1, data2, value, cb) => {
        this.setState({
            [data1]: {
                ...this.state[data1],
                [data2]: value
            }
        }, () => {
            if (typeof (cb) === "function")
                cb();
        })
    }

    changeStateRadioLevelOne = (data1, value) => {
        this.setState({
            [data1]: value
        })
    }

    formSubmit = (e) => {
        e.preventDefault();
        let data = this.state;
        let errors = {};

        if (data.name.lastName === '')
            errors.lastName = 'Please enter lastname';
        // errors.push({field: "lastName", message: "Please enter lastname"});

        let validateEmail = /^(([^<>()[]\\.,;:\s@"]+(\.[^<>()[]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

        if (data.email === '')
            errors.email = 'Please enter email';
        // errors.push({field: "email", message: "Please enter email"});
        else if (!validateEmail.test(String(data.email).toLowerCase()))
            errors.email = 'Please enter a valid email';
        // errors.push({field: "email", message: "Please enter a valid email"});

        if (data.mobile === '')
            errors.mobile = 'Please enter contact number';
        // errors.push({field: "mobile", message: "Please enter contact number"});

        if (data.enquiryType === '')
            errors.enquiryType = 'Please select nature of enquiry';
        // errors.push({field: "enquiryType", message: "Please select nature of enquiry"});

        if (!data.tnc)
            errors.tnc = 'Please accept tnc';
        // errors.push({field: "tnc", message: "Please accept tnc"});

        if (Object.keys(errors).length) {

            this.setState({
                ...this.state,
                Errors: errors
            })
        } else {
            this.Contact(data);

            this.setState({
                ...this.state,
                formSubmitted: true,
                name: {
                    title: 'mr',
                    lastName: '',
                },
                email: '',
                mobile: '',
                enquiryType: '',
                message: '',
                tnc: false,
                Errors: {},
            });

            alert('Form Submit Successful');
        }
    };

    render() {
        const {
            t,
            //i18n
        } = this.props;

        return (

            <div className="wrapper-container contact">

                <div className="page-title"><h2>{t("contact:title")}</h2></div>

                <div className="sep-0"></div>

                <div className="containerMain">

                    <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut
                        labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco
                        laboris nisi ut aliquip ex ea commodo consequat.</p>

                    <div className="content clearfix">
                        <div className="methods right">
                            <h3>Tel</h3>
                            <img src={require('../../images/mobile/07_contact_us/icon_whatsapp.png')} alt=""
                            />
                            <p><a href="tel:+85260116609">+ 852 6011 6609</a></p>

                            <div className="sep-30"></div>

                            <h3>Email</h3>
                            <p><a href="mailto:info@letspark.com.hk">info@letspark.com.hk</a></p>
                        </div>

                        <div className="form left">
                            <dl className="generalForm clearfix">

                                <div className="sep-30"></div>

                                <p className="requiredField">* Required field</p>

                                <div>
                                    <dt><h4>Name *</h4></dt>
                                    <dd>
                                        <div className="wrap clearfix">
                                            <div className="thirty">
                                                <div>
                                                    <select className="title"
                                                        value={this.state.name.title}
                                                        onChange={(e) => this.changeStateLevelTwo('name', 'title', e.target.value)}
                                                    >
                                                        <option value="mr">Mr.</option>
                                                        <option value="ms">Ms.</option>
                                                        <option value="miss">Miss.</option>
                                                    </select>
                                                </div>
                                            </div>
                                            <div className="seventy">
                                                <div>
                                                    <input className="name" type="text" placeholder=""
                                                        value={this.state.name.lastName}
                                                        onChange={(e) => this.changeStateLevelTwo('name', 'lastName', e.target.value)}
                                                    />
                                                </div>
                                            </div>
                                        </div>
                                        {Object.keys(this.state.Errors).length > 0 ? <ErrorMessage message={this.state.Errors.lastName} /> : null}
                                    </dd>
                                </div>

                                <div className="sep-25"></div>

                                <div>
                                    <dt><h4>Email Address *</h4></dt>
                                    <dd>
                                        <input type="text" placeholder=""
                                            onChange={(e) => this.changeStateLevelOne('email', e.target.value)}
                                            value={this.state.email}
                                        />
                                        {Object.keys(this.state.Errors).length > 0 ? <ErrorMessage message={this.state.Errors.email} /> : null}
                                    </dd>
                                </div>

                                <div className="sep-25"></div>

                                <div>
                                    <dt><h4>Contact Number</h4></dt>
                                    <dd>
                                        <input type="text" placeholder=""
                                            onChange={(e) => this.changeStateLevelOne('mobile', e.target.value)}
                                            value={this.state.mobile}
                                        />
                                        {Object.keys(this.state.Errors).length > 0 ? <ErrorMessage message={this.state.Errors.mobile} /> : null}
                                    </dd>
                                </div>

                                <div className="sep-25"></div>

                                <div>
                                    <dt><h4>Nature of enquiry *</h4></dt>
                                    <dd>
                                        <select
                                            value={this.state.enquiryType}
                                            onChange={(e) => this.changeStateLevelOne('enquiryType', e.target.value)}
                                        >
                                            <option value="">Please select</option>
                                            <option value="General">General enquiry</option>
                                            <option value="Comment">Comment</option>
                                            <option value="Suggestion">Suggestion</option>
                                            <option value="Media">Media enquiry</option>
                                            <option value="Business">Business development</option>
                                        </select>
                                        {Object.keys(this.state.Errors).length > 0 ? <ErrorMessage message={this.state.Errors.enquiryType} /> : null}
                                    </dd>
                                </div>

                                <div className="sep-25"></div>

                                <div>
                                    <dt><h4>Message</h4></dt>
                                    <dd>
                                        <textarea className="text" placeholder=""
                                            onChange={(e) => this.changeStateLevelOne('message', e.target.value)}
                                            value={this.state.message}
                                        ></textarea>
                                    </dd>
                                </div>

                                <div className="sep-25"></div>

                                <div>
                                    <dt></dt>
                                    <dd>
                                        <div className="checkboxFive">
                                            <div>
                                                <input type="checkbox" id="contactAgree"
                                                    checked={this.state.tnc}
                                                    onChange={(e) => this.changeStateRadioLevelOne('tnc', !this.state.tnc)}
                                                />
                                                <label htmlFor="contactAgree"></label>
                                            </div>
                                            <label htmlFor="contactAgree">I agree to the privacy statement of personal
                                                information collection</label>
                                        </div>
                                        {Object.keys(this.state.Errors).length > 0 ? <ErrorMessage message={this.state.Errors.tnc} /> : null}
                                    </dd>
                                </div>

                                <div className="sep-25"></div>

                                <div>
                                    <dt></dt>
                                    <dd>
                                        <input type="submit" className="button" value="Send"
                                            onClick={(e) => this.formSubmit(e)}
                                        />
                                    </dd>
                                </div>
                            </dl>
                        </div>
                    </div>

                </div>
            </div>
        );
    }
}

// const mapStateToProps = (state) => (
//     {
//         members: state.members,
//         router: state.router
//     }
// );

export default withTranslation()(Contact);
    //connect(mapStateToProps)(Contact);
