import React, { Component } from 'react';
import { Redirect } from 'react-router';

import ThreeSteps from '../../components/04ListASpace/ThreeSteps';
import PrevNext from '../../components/04ListASpace/PrevNext';
import ErrorMessages from '../../components/04ListASpace/ErrorMessages';

import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import * as ListParkingSpaceCreators from '../../actions/listParkingSpace';
import * as Config from "../../config";
import { withTranslation } from 'react-i18next';

// https://codepen.io/a9p6/pen/xrEKvP
class RotateIMG extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            rotation: 0
        }

        this.rotate = this.rotate.bind(this);
        this.rotateleft = this.rotateleft.bind(this);
    }

    rotate() {
        let newRotation = this.state.rotation + 90;
        if (newRotation >= 360) {
            newRotation = -360;
        }
        this.setState({
            rotation: newRotation,
        })
    }

    rotateleft() {
        let newRotation = this.state.rotation - 90;
        if (newRotation >= 360) {
            newRotation = -360;
        }
        this.setState({
            rotation: newRotation,
        })
    }

    render() {
        // console.log(this.props.width);
        // console.log(this.props.height);

        const { rotation } = this.state;
        return (
            <div>
                <div className="imageWrap">
                    <img style={{ transform: `rotate(${rotation}deg)` }} src={this.props.src}
                        className={(this.props.width > this.props.height ? 'wide' : 'long')} alt="" />
                </div>
                <div className="sep-10"></div>
                <ul className="clearfix">
                    <li className="antiClockwise"
                        onClick={this.rotateleft}
                    ></li>
                    <li className="clockwise"
                        onClick={this.rotate}
                    ></li>
                </ul>
            </div>
        )
    }
};

class StepThree extends Component {
    constructor(props) {
        super(props);
        const { dispatch } = props;
        this.SubmitStep3 = bindActionCreators(ListParkingSpaceCreators.SubmitStep3, dispatch);
        this.state = {
            file: [],
            formSubmitted: false,
            formSubmitting: false,
            imagePreviewUrl: [],
            ownerName: '',
            ownerName2: '',
            Errors: [],
            moreOwner: false
        };
    }

    componentDidMount = () => {
        // const storageData = JSON.parse(sessionStorage.getItem('state'));
        // console.log(storageData);

        if (this.props.listParkingSpace.StepThree.length !== 0) {
            this.setState(this.props.listParkingSpace.StepThree)
            this.setState({
                formSubmitted: false,
                Errors: [],
            });
        }
    }

    componentDidUpdate = () => {
        // sessionStorage.setItem('state', JSON.stringify(this.state));
        // console.log(this.state);
    }

    _handleImageChange(e, id) {
        e.preventDefault();

        let reader = new FileReader();
        let uploaded_file = e.target.files[0];

        reader.onloadend = (file) => {

            let image = new Image();
            image.src = file.target.result;

            image.onload = () => {
                // access image size here
                let width = image.width;
                let height = image.height;

                this.setState({
                    file: {
                        ...this.state.file,
                        [id]: {
                            src: image.src,
                            width: width,
                            height: height,
                        }
                    },
                    imagePreviewUrl: {
                        ...this.state.imagePreviewUrl,
                        [id]: reader.result
                    }
                });
            };

            this.ApiUploadImage(id, uploaded_file);
        }

        reader.readAsDataURL(uploaded_file)
    }

    ApiUploadImage = (id, image) => {
        let formData = new FormData();
        formData.append("image", image);


        fetch(Config.API_URL_BASE + 'upload', {
            method: 'post',
            headers: new Headers({
                //'Authorization': 'Basic ' + btoa(this.props.members.Login.userToken + ':' + Config.UAT_FEED_PASSWORD),
                // 'Accept': 'application/json',
                // "Content-Type": "multipart/form-data"
            }),
            body: formData,
        })
            // .then(r => r.json().then(data => ({ status: r.status, body: data })
            // ))
            .then(response => response.json())
            .then(response => {

                this.setState({
                    file: {
                        ...this.state.file,
                        [id]: {
                            src: response.path,
                        }
                    },
                });
            })
            // .then(obj => {
            //
            //     if (obj.status === 200) {
            //
            //     } else if (obj.status === 400) {
            //
            //         this.setState({
            //
            //             errors: obj.body.messages
            //         });
            //     }
            // })
            .catch(error => {

                console.log("error");

                console.log(error);

            });
    }

    _removeImage(id) {
        this.setState({
            file: {
                ...this.state.file,
                [id]: ''
            },
            imagePreviewUrl: {
                ...this.state.imagePreviewUrl,
                [id]: ''
            }
        });
    }

    changeStateLevelOne = (data1, value) => {
        this.setState({
            [data1]: value
        })
    }

    handleExchange = (target) => {
        if (target === 'OneTwo') {
            this.setState({
                ...this.state,
                file: {
                    ...this.state.file,
                    1: this.state.file[2],
                    2: this.state.file[1],
                },
                imagePreviewUrl: {
                    ...this.state.imagePreviewUrl,
                    1: this.state.imagePreviewUrl[2],
                    2: this.state.imagePreviewUrl[1],
                }
            })
        } else if (target === "TwoThree") {
            this.setState({
                ...this.state,
                file: {
                    ...this.state.file,
                    2: this.state.file[3],
                    3: this.state.file[2],
                },
                imagePreviewUrl: {
                    ...this.state.imagePreviewUrl,
                    2: this.state.imagePreviewUrl[3],
                    3: this.state.imagePreviewUrl[2],
                }
            })
        }
    }

    handlemoreOwner = () => {
        this.setState({
            ...this.state,
            moreOwner: !this.state.moreOwner
        })
    }

    submitForm = (e) => {
        e.preventDefault();

        let errors = [];

        if (this.state.ownerName === "") {
            errors.push({ field: "ownerName", message: "Please enter the name of owner." });
        }

        if (errors.length) {
            this.setState({
                ...this.state,
                Errors: errors
            })
        } else {
            let data = this.state;

            this.SubmitStep3(data);

            this.setState({
                ...this.state,
                formSubmitted: true
            });
        }
    };

    render() {
        const {
            //t, 
            i18n
        } = this.props;
        // console.log(this.state);

        if (this.props.members.Login.length === 0) {
            return <Redirect to={"/" + i18n.language + "/memberLogin"} />;
        }

        if (this.props.listParkingSpace.StepOne.length === 0) {
            return <Redirect to={"/" + i18n.language + "/stepOne"} />;
        }

        if (this.props.listParkingSpace.StepTwo.length === 0) {
            return <Redirect to={"/" + i18n.language + "/stepTwo"} />;
        }

        if (this.state.formSubmitted === true) {
            return <Redirect to={"/" + i18n.language + "/preview"} />;
        }

        return (
            <div className="wrapper-container memberSettings">
                <div className="page-title">
                    <h2>List Your Space</h2>
                    <div className="sep-0"></div>
                    <ThreeSteps
                        selected={3}
                        stepsLeft={1}
                    />
                </div>

                <div className="containerMain">
                    <div className="content">

                        <h3>Photos</h3>

                        <div className="sep-30"></div>

                        <dl className="generalForm clearfix">
                            <div>
                                <dt>
                                    <p>Image(s) gallery<br />
                                        (allow PNG, JPEG format)<br />
                                        (first one is the cover photo)</p>
                                </dt>
                                <dd>
                                    <ul className="image-upload clearfix">
                                        <div className="oneThird">
                                            <div>
                                                <li>
                                                    <div>
                                                        <input type="file" id="file-1"
                                                            onChange={(e) => this._handleImageChange(e, 1)} />
                                                        <label htmlFor="file-1"
                                                            className={(this.state.file[1] ? 'hide' : '')}></label>
                                                        <div className={"remove " + (this.state.file[1] ? 'show' : '')}
                                                            onClick={(e) => this._removeImage(1)}></div>
                                                        {this.state.file[1] ?
                                                            <RotateIMG src={this.state.imagePreviewUrl[1]}
                                                                width={this.state.file[1].width}
                                                                height={this.state.file[1].height} /> : ''}
                                                    </div>
                                                </li>
                                            </div>
                                        </div>
                                        <div className="oneThird">
                                            <div>
                                                <li>
                                                    <span className="exchange"
                                                        onClick={(e) => this.handleExchange('OneTwo')}
                                                    ></span>
                                                    <div>
                                                        <input type="file" id="file-2"
                                                            onChange={(e) => this._handleImageChange(e, 2)} />
                                                        <label htmlFor="file-2"
                                                            className={(this.state.file[2] ? 'hide' : '')}></label>
                                                        <div className={"remove " + (this.state.file[2] ? 'show' : '')}
                                                            onClick={(e) => this._removeImage(2)}></div>
                                                        {this.state.file[2] ?
                                                            <RotateIMG src={this.state.imagePreviewUrl[2]}
                                                                width={this.state.file[2].width}
                                                                height={this.state.file[2].height} /> : ''}
                                                    </div>
                                                </li>
                                            </div>
                                        </div>
                                        <div className="oneThird">
                                            <div>
                                                <li>
                                                    <span className="exchange"
                                                        onClick={() => this.handleExchange('TwoThree')}
                                                    ></span>
                                                    <div>
                                                        <input type="file" id="file-3"
                                                            onChange={(e) => this._handleImageChange(e, 3)} />
                                                        <label htmlFor="file-3"
                                                            className={(this.state.file[3] ? 'hide' : '')}></label>
                                                        <div className={"remove " + (this.state.file[3] ? 'show' : '')}
                                                            onClick={(e) => this._removeImage(3)}></div>
                                                        {this.state.file[3] ?
                                                            <RotateIMG src={this.state.imagePreviewUrl[3]}
                                                                width={this.state.file[3].width}
                                                                height={this.state.file[3].height} /> : ''}
                                                    </div>
                                                </li>
                                            </div>
                                        </div>
                                    </ul>
                                </dd>
                            </div>

                            <div className="sep-40"></div>

                            <div>
                                <dt>
                                    <p>Name of Owner *</p>
                                </dt>
                                <dd>
                                    <input type="text" name="ownerName"
                                        placeholder="Name"
                                        value={this.state.ownerName}
                                        onChange={(e) => this.changeStateLevelOne('ownerName', e.target.value)}
                                    />
                                    <ErrorMessages errors={this.state.Errors} />
                                    {!this.state.moreOwner &&
                                        <span className="addmore"
                                            onClick={() => this.handlemoreOwner()}>add more</span>
                                    }
                                </dd>
                            </div>
                            {this.state.moreOwner &&
                                <div className="sep-40"></div>
                            }
                            {this.state.moreOwner &&
                                <div>
                                    <dt>
                                        <p>Name of Owner</p>
                                    </dt>
                                    <dd>
                                        <input type="text" name="ownerName2"
                                            placeholder="Name2"
                                            value={this.state.ownerName2}
                                            onChange={(e) => this.changeStateLevelOne('ownerName2', e.target.value)}
                                        />
                                        {this.state.moreOwner &&
                                            <span className="removemore"
                                                onClick={() => this.handlemoreOwner()}>remove</span>
                                        }
                                    </dd>
                                </div>
                            }
                        </dl>

                        <div className="sep-25"></div>

                        <PrevNext
                            prevHas={true}
                            prevURL={"/" + i18n.language + "/stepTwo"}
                            prevText='Back'
                            nextURL={"/" + i18n.language + "/preview"}
                            nextText='Preview'
                            progress="three"
                            value="3/3"
                            prevHandler={null}
                            nextHandler={(e) => this.submitForm(e)}
                        />
                    </div>
                </div>
            </div>
        );
    }
}

const mapStateToProps = state => (
    {
        members: state.members,
        listParkingSpace: state.listParkingSpace
    }
);

export default withTranslation()(connect(mapStateToProps)(StepThree));
//connect(mapStateToProps)(DetailShare);
