import React, { Component } from 'react';

class Cancellation_zh_HK extends Component {
    render() {
        return (
            <div className="cancellation">
                <p>Once a Booking has been completed, it cannot be cancelled other than in accordance with the terms expressly set out in the Licence Agreement, and also the service fee shall not be refundable as well - Here are some termination scenarios with the particular party involvement such as:</p>
                
                <ol>
                    <li><strong><u>Termination by Parker &ndash; Hourly Parking</u></strong>
                        <ul>
                            <li>The Parker is not able to get the refund from this case since the Parker has terminated the license agreement by themselves, and the Owner is entitled to get the payment via Let&rsquo;s Park with the service fee paid.</li>
                        </ul>
                    </li>
                </ol>
                
                <ol start="2">
                    <li><strong><u>Termination by Owner &ndash; Hourly Parking</u></strong>
                        <ul>
                            <li>The Parker is eligible to get the refund from the scheme of &ldquo;Parker Protection Guarantee&rdquo; and entitle to recoup the additional cost due to the Owner&rsquo;s breach. Let&rsquo;s Park can use reasonable commercial effort to recoup the additional for Parker, but Let&rsquo;s Park do not have any liability on it</li>
                        </ul>
                    </li>
                </ol>
                
                <ul>
                    <li>The Owner is responsible to pay the remaining service fee to Let&rsquo;s Park.</li>
                </ul>
                
                <ol start="3">
                    <li><strong><u>Termination by Parker &ndash; Monthly Parking / Time Share</u></strong>
                        <ul>
                            <li>The Parker is not able to get the refund &amp; deposit from this case since the Parker has terminated the license agreement by themselves, and the Owner is entitled to get the payment via Let&rsquo;s Park with the service fee paid.</li>
                        </ul>
                    </li>
                </ol>
                
                <ul>
                    <li>Let&rsquo;s Park can use reasonable commercial effort to recoup the remaining licence fee to Owner from asking the Parker, but Let&rsquo;s Park do not have any liability on it</li>
                </ul>
                
                <ul>
                    <li>Owner is responsible for his liability to Let&rsquo;s Park of the remaining service fee.</li>
                </ul>
                
                <ol start="4">
                    <li><strong><u>Termination by Owner &ndash; Monthly Parking / Time Share</u></strong>
                        <ul>
                            <li>The Parker is eligible to get the refund and deposit through the scheme of Parker Protection Guarantee&rdquo; and entitle to recoup the additional cost incurred from the action of Owner&rsquo;s breach. Let&rsquo;s Park can use reasonable commercial effort to recoup the additional cost for Parker, but Let&rsquo;s Park do not have any liability on it</li>
                        </ul>
                    </li>
                </ol>
                
                <ul>
                    <li>Owner is responsible for his liability to Let&rsquo;s Park of the remaining service fee.</li>
                </ul>
                
                
                
                <ul>
                    <li>We shall not be liable to you for any decision of fact we may make pursuant to the provisions of these Terms or the Licence Agreement which you agree to be final and binding on you.</li>
                </ul>
            </div>
        );
    }
}

