class Terms extends Component {
    render() {
        const {
            //t, 
            i18n
        } = this.props;
        return (
            <div className="wrapper-container media">

                <div className="page-title"><h2>Terms and Conditions of Services</h2></div>

                <div className="sep-0"></div>

                <div className="containerMain">
                    <div className="content clearfix">
                        {i18n.language === 'zh-HK'
                            ? <TermsZHHK />
                            : <TermsENUS />
                        }
                    </div>
                </div>
            </div >
        );
    }
}

